import { createStore, Store, compose, applyMiddleware } from 'redux';
import createSagaMiddleware from 'redux-saga';
import { all, call, spawn } from 'redux-saga/effects';

import { omit } from 'lodash';
import { root_reducer, StoreState, model_sagas } from '../redux';
import { AIModelForSave } from '../models/def';
import { machine_learning } from '../controllers';

function create_store() {
  const middlewares = [];
  const saga_middleware = createSagaMiddleware();
  middlewares.push(saga_middleware);

  const store = createStore(
    root_reducer,
    // ((main_state ? {
    //   main_states: _.defaults(initial_states, main_state),
    // } : undefined) as StoreState),
    (window as any).__REDUX_DEVTOOLS_EXTENSION__ ?
      compose(applyMiddleware(...middlewares), (window as any).__REDUX_DEVTOOLS_EXTENSION__()) :
      applyMiddleware(...middlewares),
  );
  saga_middleware.run(saga_root);
  return store;
}

function* saga_root() {
  const sagas = [
    model_sagas,
  ];
  yield all(
    sagas.map((saga) =>
      spawn(function*() {
        while (true) {
          try {
            yield call(saga);
          } catch (e) {
            console.error(e);
          }
        }
      }),
    ),
  );
}

const get_path_object = (obj:{[key:string]:any}, object_path:string):any => {
  const path_list = object_path.split('.');
  const path = path_list.shift();
  const remain_path = path_list.join('.');
  return path && obj[path] ? get_path_object(obj[path], remain_path) : obj;
};

class StoreManager {
  private store:Store<StoreState>;
  constructor() {
    this.store = create_store();
  }
  public get_store():Store<StoreState> {
    return this.store;
  }
  public get_state() {
    return this.store.getState();
  }
  public subscribe(listener:() => void) {
    this.store.subscribe(listener);
  }
  public dispatch(action:any) {
    return this.store.dispatch(action);
  }

  public watch(path:string, cb:(old_value:any, new_value:any) => void) {
    let current_value = get_path_object(this.get_state(), path);
    this.subscribe(() => {
      const new_value = get_path_object(this.get_state(), path);
      const old_value = current_value;
      if (new_value !== old_value) {
        current_value = new_value;
        cb(old_value, new_value);
      }
    });
  }

  public get_save_info():AIModelForSave {
    const model_state = this.get_state().model_state;
    const current_net = model_state.net;
    if (!current_net) {
      const net = machine_learning.generate_net(model_state.features.length, model_state.classifications.length);
      model_state.net = net;
    }
    // kitten
    const data_state = this.get_state().data_state;
    for (let i = 0; i < data_state.symbol_index.length; i++) {
      data_state.symbol_index[i] = [-1, -1];
    }
    // 
    const data_boundary = machine_learning.get_data_boundary(data_state.data, model_state.features.length);
    model_state.max = data_boundary.max;
    model_state.min = data_boundary.min;
    return {
      model_state: model_state,
      training_state: omit(this.get_state().training_state, 'data_flag'),
      data_state: data_state,
    };
  }
}

export const store_manager = new StoreManager();
