import * as React from 'react';
import './style.scss';

interface DropdownProps {
  label?:string;
  current_selection:string | number;
  selections:(string | number)[];
  choose_callback:(selection:string | number) => void;
  disabled:boolean;
}

export class Dropdown extends React.Component<DropdownProps, {}> {
  private set_selection = (selection:string|number) => {
    if (selection === this.props.current_selection) {
      return;
    }

    this.props.choose_callback(selection);
    // Hide the dropdown menu
    const current_focus = document.activeElement as HTMLDivElement;
    if (!current_focus) { return; }
    current_focus.blur();
  }

  render() {
    return (
      <div styleName="dropdown-wrapper">
        {this.props.label && <div styleName={`label ${this.props.disabled ? 'disabled' : ''}`}>{this.props.label}</div>}
        <div
            className="th1-border-focus-0 th1-border-impt-7"
            styleName={`input-area ${this.props.disabled ? 'disabled' : ''}`}
            tabIndex={-1}
        >
          {this.props.current_selection}
          <div
              styleName="menu"
          >
            {this.props.selections.map((selection, index) =>
              <div
                  styleName={`selection ${selection === this.props.current_selection ? 'selected' : ''}`}
                  key={index}
                  onClick={this.set_selection.bind(this, selection)}
                  className="th1-bg-hover-9"
              >
                {selection}
              </div>)}
          </div>
        </div>
      </div>
    );
  }
}

export const DropdownComponent:React.ComponentClass<DropdownProps> = Dropdown;
