export interface MenuItem {
  text:string;
  cb:(e:any) => void;
  disabled?:boolean;
}

export type MenuItems = MenuItem[];

export interface ContextMenuState {
  readonly x:number;
  readonly y:number;
  readonly menu_items:MenuItems;
  is_show?:boolean;
}

const initial_context_menu_state:ContextMenuState = {
  is_show: false,
  x: 0,
  y: 0,
  menu_items: [],
};

// Actions
export const SHOW_CONTEXT_MENU = 'ai_lab/show_context_menu';
export function show_context_menu(options:ContextMenuState) {
  return { type: SHOW_CONTEXT_MENU, payload: options };
}

export const HIDE_CONTEXT_MENU = 'ai_lab/hide_context_menu';
export function hide_context_menu() {
  return { type: HIDE_CONTEXT_MENU };
}

export function context_menu_reducer(
    state:ContextMenuState = initial_context_menu_state,
    action:any):ContextMenuState
{
  switch (action.type) {
    case SHOW_CONTEXT_MENU:
      return Object.assign({ is_show: true }, action.payload);
    case HIDE_CONTEXT_MENU:
      return initial_context_menu_state;
    default:
      return state;
  }
}
