import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { cloneDeep } from 'lodash';
import { StoreState } from '../../../redux';
import { machine_learning } from '../../../controllers';
import { is_valid_number } from '../../../models/utils';
import { show_notice } from '../../../connected_deps';

import './style.scss';
interface Props extends InjectedIntlProps {
  features:string[];
  classifications:string[];
}
interface State {
  probabilities:number[];
  minimized:boolean;
  input:string[];
}

class AIModelTest extends React.Component<Props, State> {
  private last_value:string | number;

  constructor(props:Props) {
    super(props);
    this.state = {
      probabilities: [],
      minimized: false,
      input: Array(this.props.features.length).fill('0'),
    };
  }

  private handle_input_change = (index:number, new_val:string) => {
    const input = cloneDeep(this.state.input);
    input[index] = new_val;
    this.setState({
      input,
    });
  }

  private get_input_area() {
    const inputs = this.props.features.map((feature, index) => <div styleName="feature-wrapper" key={index}>
      <div styleName="feature-label">{feature}</div>
      <div styleName="feature-input">
        <input
            type="text"
            className="th1-border-impt-6"
            value={this.state.input[index]}
            onChange={(e) => {
              // Prevent the non-minus, non-number and non-dot chars being input
              const new_val = e.target.value.replace(/[^\-\d.]/g, '');
              this.handle_input_change(index, new_val);
            }}
            onFocus={(e) => { this.last_value = e.target.value; }}
            onBlur={(e) => {
              let val;
              if (is_valid_number(e.target.value)) {
                val = String(parseFloat(e.target.value));
              } else {
                val = this.last_value;
              }
              val = String(val);

              this.handle_input_change(index, val);
            }} />
      </div>
    </div>);
    return <div styleName="feature-area">{inputs}</div>;
  }

  private get_result() {
    if (this.state.probabilities.length === 0) { return; }
    const result = this.props.classifications.map((classification, index) => ({ classification, prob: this.state.probabilities[index] }));
    result.sort((x, y) => y.prob - x.prob);
    const result_list = result.map((res, index) => {
      const res_prob = res.prob * 100.0;

      return <div styleName="line" key={index}>
        <div styleName="res-area-content">
          <div styleName="classification">{res.classification}</div>
        </div>
        <div styleName="res-area-content">{`${isNaN(res_prob) ? 0 : res_prob.toFixed(2) }%`}</div>
      </div>;
    });
    result_list.unshift(
      <div styleName="line" key="title">
        <div styleName="res-area-header">
          {this.props.intl.formatMessage({ id: 'classification' })}
        </div>
        <div styleName="res-area-header">
          {this.props.intl.formatMessage({ id: 'probability' })}
        </div>
      </div>,
    );
    return <div styleName="res-wrapper">
      <div styleName="res-header">
        {this.props.intl.formatMessage({ id: 'result' })}
        <div styleName="res-header-notice">
          <span
              styleName="res"
              className="th1-text-0 th1-text-s2"
          >
            {result[0].classification}
          </span></div>
      </div>
      <div styleName="res-area">{result_list}</div>
    </div>;
  }

  render() {
    return (
      this.state.minimized ?
        <div
            className="th1-bg-9"
            styleName="expand-wrapper"
            onClick={() => { this.setState({ minimized: false }); }}
        >
          <div styleName="expand" />
        </div> :
        <div styleName="test-wrapper">
          <div
              styleName="test-header"
              className="th1-border-impt-0 th1-bg-8"
          >
            {this.props.intl.formatMessage({ id: 'test' })}
          </div>
          {this.get_input_area()}
          <div styleName="btn_wrapper">
            <div
                styleName="btn-test"
                key="test"
                className="th1-bg-0"
                data-report-click="AI2-"
                onClick={() => {
                  const input = this.state.input.map(parseFloat);

                  if (
                    input.some((feature) => isNaN(feature))
                  ) {
                    show_notice({
                      id: 'feature_is_needed_to_predict',
                    });
                    return;
                  }

                  const probabilities = machine_learning.predict(input);
                  this.setState({ probabilities });
                }}>
              {this.props.intl.formatMessage({ id: 'test' })}
            </div>
          </div>
          {this.get_result()}
          <div styleName="minimize" onClick={() => { this.setState({ minimized: true }); }} />
        </div>
    );
  }
}

function map_state_to_props(state:StoreState) {
  return {
    features: state.model_state.features,
    classifications: state.model_state.classifications,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
    },
    dispatch);
}

export const AIModelTestComponent = connect(map_state_to_props, map_dispatch_to_props)(injectIntl((AIModelTest)));