import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { injectIntl, InjectedIntlProps } from 'react-intl';

import { StoreState } from '../../redux';
import { change_model_name } from '../../redux/reducers/model_reducer';
import { get_validated_name } from '../../models/utils';
import { report_event } from '../../connected_deps';

import './style.scss';

type Props = InjectedIntlProps & ReturnType<typeof map_state_to_props> & ReturnType<typeof map_dispatch_to_props>

interface State {
  //  state
  name:string;
}

class ModelName extends React.Component<Props, State> {
  constructor(props:Props) {
    super(props);

    let default_name = props.model_name;
    if (!default_name) {
      default_name = props.intl.formatMessage({ id: 'untitled_model' });
      props.change_model_name(default_name);
    }
    this.state = {
      name: default_name,
    };
  }

  UNSAFE_componentWillReceiveProps(nextprops:Props) {
    if (nextprops.model_name !== this.state.name) {
      this.setState({
        name: nextprops.model_name,
      });
      this.input.value = nextprops.model_name;
    }
  }

  private input:HTMLInputElement;

  render() {
    return (
      <div styleName="model-name">
        <div styleName="prefix">
          {this.props.intl.formatMessage({ id: 'ai_lab/model_name_prefix' })}
        </div>
        <input
            className="th1-bg-8 th1-border-hover-0 th1-border-impt-focus-0 th1-text-t5"
            maxLength={10}
            value={this.state.name}
            styleName="model-name-input"
            ref={(el) => {if (el) { this.input = el; }}}
            onFocus={(e) => {
              this.input && this.input.select();
            }}
            onChange={(e) => {
              this.setState({ name: e.target.value });
            }}
            onKeyUp={(e:React.KeyboardEvent<HTMLInputElement>) => {
              if (e.keyCode === 13) { (e.target as HTMLInputElement).blur(); }
            }}
            onBlur={(e:React.FocusEvent<HTMLInputElement>) => {
              // 
              const model_name = get_validated_name(e.target.value, this.props.model_name, []);

              this.setState({
                name: model_name,
              });
              this.props.change_model_name(model_name);
              report_event('AI2-');
            }}
        />
      </div>
    );
  }

}


function map_state_to_props(state:StoreState) {
  return {
    model_name: state.model_state.model_name,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      change_model_name,
    },
    dispatch,
  );
}

export const ModelNameComponent =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl((ModelName)));