import { connect } from 'react-redux';
import { IntlProvider } from 'react-intl';

import { StoreState } from './redux';
import { get_locale_data } from './i18n';

function map_state_to_props(state:StoreState) {
  const { locale, messages } = get_locale_data();
  return {
    locale,
    messages,
    //  key https://github.com/formatjs/react-intl/issues/371#issuecomment-275703796
    key: locale,
  };
}

export const ConnectedIntlProvider:any = connect(map_state_to_props)(IntlProvider);