import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { StoreState } from '../redux';
import { hide_notice } from '../redux/reducers/notice_reducer';

import './style.scss';

interface NoticeProps extends InjectedIntlProps {
  notice_content:string;
  timeout:number;
  hide_notice:typeof hide_notice;
}

interface NoticeState {
  is_disappearing:boolean;
}

class Notice extends React.Component<NoticeProps, NoticeState> {
  constructor(props:NoticeProps) {
    super(props);
    this.state = {
      is_disappearing: false,
    };
  }

  componentDidMount() {
    const { timeout } = this.props;
    this.timer = setTimeout(
      () => {
        this.setState({ is_disappearing: true });
      },
      timeout);
  }

  componentWillUnmount() {
    clearTimeout(this.timer);
  }

  private timer:number;

  render() {
    return (
      <div
          styleName={this.state.is_disappearing ? 'notice-top-hide' : 'notice-top'}
          onTransitionEnd={this.props.hide_notice}
      >
        {this.props.notice_content}
      </div>
    );
  }
}

function map_states_to_props(state:StoreState) {
  return {
    notice_content: state.notice_state.content,
    timeout: state.notice_state.timeout,
  };
}
function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      hide_notice,
    },
    dispatch);
}

export const NoticeComponent =
  connect(
    map_states_to_props,
    map_dispatch_to_props,
  )(injectIntl((Notice)));

