import * as React from 'react';
import './style.scss';

interface SwitchProps {
  current_status:boolean;
  set_status:(is_custom_mode:boolean) => void;
  label?:string;
  hint?:string;
}

export class Switch extends React.Component<SwitchProps, {}> {
  toggle_mode = () => {
    this.props.set_status(!this.props.current_status);
  }

  render() {
    return (
      <div styleName="switch_container">
        <div styleName="switch_label">
          <div>{this.props.label}</div>
          {this.props.hint && <div styleName="switch_container_hint">{this.props.hint}</div>}
        </div>
        <label styleName="switch">
          <input
              type="checkbox"
              name="mode"
              checked={this.props.current_status}
              onChange={this.toggle_mode}
          />
          <span styleName="slider" />
        </label>
      </div>
    );
  }
}

export const SwitchComponent:React.ComponentClass<SwitchProps> = Switch;
