import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { StoreState, close_confirm_dialog } from '../../redux';

import './style.scss';

interface ConnectedProps extends InjectedIntlProps {
  confirm_callback:Function;
  title:string;
  content:string;
  pos_btn:string;
  neg_btn:string;
  close_confirm_dialog:typeof close_confirm_dialog;
}

class Confirm extends React.Component<ConnectedProps> {
  render() {
    return (
      <div styleName="dialog-container">
        <div
            styleName="mask"
            onClick={this.props.close_confirm_dialog} />
        <div styleName="dialog-wrapper">
          <div styleName="dialog-title">
            {this.props.title}
          </div>
          <div styleName="dialog-content">
            {this.props.content}
          </div>
          <div styleName="btn-group">
            {this.props.neg_btn &&
            <div
                styleName="btn negative left"
                onClick={this.props.close_confirm_dialog}>
              {this.props.neg_btn }
            </div>}
            <div
                styleName="btn positive"
                onClick={() => { this.props.confirm_callback(); this.props.close_confirm_dialog(); }}>
              {this.props.pos_btn }
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function map_state_to_props(state:StoreState) {
  return {
    title: state.confirm_dialog_state.title,
    content: state.confirm_dialog_state.content,
    confirm_callback: state.confirm_dialog_state.callback,
    pos_btn: state.confirm_dialog_state.pos_btn,
    neg_btn: state.confirm_dialog_state.neg_btn,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      close_confirm_dialog,
    },
    dispatch);
}

export const ConfirmDialog =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl((Confirm)));
