import { intl } from './i18n';
import { store_manager } from './controllers';
import {
  show_notice as native_show_notice,
  show_confirm_dialog as native_show_confirm_dialog,
  Theme,
} from './redux';
import { get_props_show_notice, get_props_show_confirm_dialog, NoticeType, get_props_report_event } from './deps';

function get_intl(id:string) {
  return intl().formatMessage({ id });
}

/**
 *  show_notice  show_notice
 * @param options 
 */
export function show_notice(options:{
  id:string,
  type?:NoticeType,
}) {
  const { id, type } = options;
  const content = get_intl(id);

  const props_show_notice = get_props_show_notice();
  if (props_show_notice) {
    props_show_notice(content, type);
    return;
  }

  store_manager.dispatch(native_show_notice(content));
}


/**
 *  show_confirm_dialog  show_confirm_dialog
 *
 * @export
 * @param {Function} callback  callback
 * @param {string} title 
 * @param {string} content 
 * @param {{ text:string, type?:string }} right 
 * @param {string} [left] 
 * @returns
 */
export function show_confirm_dialog(callback:Function, title:string, content:string, right:{ text:string, type?:string }, left?:string) {
  title = get_intl(title);
  right.text = get_intl(right.text);
  left && (left = get_intl(left));

  const props_show_confirm_dialog = get_props_show_confirm_dialog();
  if (props_show_confirm_dialog) {
    props_show_confirm_dialog(callback, title, content, right, left);
    return;
  }

  store_manager.dispatch(native_show_confirm_dialog(callback, title, content, right.text, left));
}

/**
 * class2
 * @param classname 
 * @param current_theme 
 */
export function get_dynamic_theme_class(
    classname:string,
    current_theme:Theme,
    minus_num:number = 2,
) {
  if (!current_theme.light_theme_colors) {
    return classname;
  }
  return classname.replace(new RegExp(/-\d+(\.\d+)?/, 'g'), (matched) => {
    const num = Math.abs(parseInt(matched)) - minus_num; // 2
    switch (num) {
      case -1:
        return '-m1';
      case -2:
        return '-m2';
      default:
        return `-${num}`;
    }
  });
}

export function report_event(ev:string) {
  const report_event = get_props_report_event();
  report_event && report_event(ev);
}
