export interface ConfirmDialogState {
  callback:Function;
  title:string;
  content:string;
  pos_btn:string;
  neg_btn:string;
  is_show:boolean;
}

export const initial_confirm_dialog_state:ConfirmDialogState = {
  callback: Function,
  title: '',
  content: '',
  pos_btn: '',
  neg_btn: '',
  is_show: false,
};

// Actions
export const SHOW_CONFIRM_DIALOG = 'ai_lab/show_confirm_dialog';
export function show_confirm_dialog(callback:Function, title:string, content:string, pos_btn:string, neg_btn?:string) {
  return { type: SHOW_CONFIRM_DIALOG, payload: { callback, title, content, pos_btn, neg_btn } };
}

export const CLOSE_CONFIRM_DIALOG = 'ai_lab/close_confirm_dialog';
export function close_confirm_dialog() {
  return { type: CLOSE_CONFIRM_DIALOG };
}

export function confirm_dialog_reducer(state = initial_confirm_dialog_state, action:any) {
  switch (action.type) {
    case SHOW_CONFIRM_DIALOG:
      return Object.assign({}, state, {
        is_show: true,
        callback: action.payload.callback,
        title: action.payload.title,
        content: action.payload.content,
        pos_btn: action.payload.pos_btn,
        neg_btn: action.payload.neg_btn,
      });
    case CLOSE_CONFIRM_DIALOG:
      return initial_confirm_dialog_state;
    default:
      return state;
  }
}