import { LanguageDataType } from '@src/i18n';
import { ShowNoticeFunction, ShowConfirmDialogFunction, DataReportFunc } from '../deps';

/**
 * Expressions in this project:
 *
 * iteration - call the trainer.train function once, i.e. train the net exactly once per iteration.
 * round - train the net several (ITER_PER_ROUND) times and draw the preview diagram once
 *         i.e. communicate between main thread (control layout) and worker (train & preview) once every round.
 *
 */

export enum TrainingStep {
  EDITING_DATA,
  CHOOSING_DATA_SET,
  IMPORTING,
  TRAINING,
}
export enum ModelType {
  IMG,
  DATA,
}
export const model_type_dict = ['img_recognition', 'data_recognition'];

/**
 * Predict result and redraw the preview every round.
 * Train for 5 iterations per round.
 */
export const ITER_PER_ROUND = 5;

/**
 * Target range of training.
 * Project all input data into this range before training to improve training perfermance.
 */
export const TARGET_RANGE = [-5, 5];

/**
 * The edge of preview diagram.
 * Actual size of the diagram is 50x50
 */
export const SIZE_PREVIEW = 50;

/**
 * Range of total training iterations in custom mode.
 */
export const RANGE_TRAINING_ITERATIONS = [50, 100, 200, 500, 1000, 2000];

/**
 * Default training iterations for custom mode without user settings.
 */
export const DEFAULT_TRAINING_ITERATIONS = 100;

/**
 * Selections for learning rate
 */
export const RANGE_LEARNING_RATE = [0.00001, 0.0001, 0.001, 0.003, 0.01, 0.03, 0.1, 0.3, 1, 3, 10];

/**
 * Default learning rate without user settings.
 */
export const DEFAULT_LEARNING_RATE = 0.03;

/**
 * Target accuracy (least accuracy) of training.
 * Training should be stoped when target accuracy and target loss are both reach.
 */
export const TARGET_ACCURACY = 0.95;

/**
 * Target loss (max loss) of training.
 * Training should be stoped when target accuracy and target loss are both reach.
 */
export const TARGET_LOSS = 0.05;

/**
 * Training should be stopped when it is stable, i.e. more iterations are meaningless for improving accuracy and reduce loss.
 * Only judge stable status when training rounds > 100 (training iterations: STABLE_BASE * ITER_PER_ROUND, in current case, 500)
 */
export const STABLE_BASE = 100;

/**
 * Parameter to decide wheater a model is stable.
 */
export const STABLE_STANDARD = 0.0001;

/**
 * The judgement of stability happens between current round and the round before STABLE_ROUND rounds.
 * When the differences of current accuracy and loss and that round is within STABLE_STANDARD, the model is stable.
 */
export const STABLE_ROUND = 500 / ITER_PER_ROUND; // 500 iterations, 100 rounds here

/**
 * Length of empty data of every classification at the beginning of opening ai lab.
 */
export const DEFAULT_DATA_LENGTH = 5;

/**
 * Least number of data rows per classification to start training.
 */
export const MIN_DATA_LENGTH = 5;

/**
 * Range of number of features.
 */
export const RANGE_NUM_FEATURES = [1, 4];

/**
 * Range of number of classifications.
 */
export const RANGE_NUM_CLASSES = [2, 4];

/**
 * Colors for drawing preview diagram, indexed by classification.
 */
export const CLASS_COLORS = [
  '#0057FF',
  '#FF9948',
  '#3DD1AE',
  '#FF4B4B',
];

export const ACC_LINE_COLOR = '#FF9948';
export const LOSS_LINE_COLOR = '#757575';

/**
 * Data structure of the message data from main thread to worker.
 * Each message starts a round.
 */
export type TrainMessage = PreviewMessage | StartMessage | ContinueMessage;

type PreviewMessage = {
  type:'preview',
  data:number[][][], // data, indexed by classes -> row_index -> feature_index
  dimension:number, // length of features
  hidden_layers:number[], // structure of hidden layers
};

type StartMessage = {
  type:'start',
  should_reset:boolean,

  data?:number[][][], // data, indexed by classes -> row_index -> feature_index
  dimension?:number, // length of features
  hidden_layers?:number[], // structure of hidden layers

  // Training parameters
  is_custom_mode?:boolean,
  // Can be modified any time
  training_iterations?:number,
  learning_rate?:number,
};

type ContinueMessage = {
  type:'continue',
  should_reset:boolean,

  // Can be modified any time
  training_iterations?:number,
  learning_rate?:number,
};

/**
 * Data structure of the message data from work to main thread at the end of every round.
 */
export interface TrainResult {
  /**
   * the value 'preview' is used when there is no model available
   * i.e. before any train started, show the preview result of the random network
   */
  type:'preview' | 'update';
  net:string;
  draw_data:DrawData[][];
  accuracy:number;
  loss:number;
}

/**
 * 1. In status 'wait_start', the last train cannot be continued.
 *    i.e. although in custom mode, the user can only restart
 * 2. 'training' status indicates the train is processing.
 * 3. In status 'stopped', user can continue the train without resetting net in custom mode.
 */
export type TrainingStatus = 'wait_start' | 'training' | 'stopped';

/**
 * Data structure of official data sets.
 */
export interface DataSet {
  name:string;
  classifications:string[];
  preview:string;
  source:(string | number)[][];
  has_label:boolean;
}

/**
 * Default hidden layers without user settings.
 */
export const DEFAULT_HIDDEN_LAYERS:number[] = [4, 4];

/**
 * Data structure for drawing preview diagram.
 */
export interface DrawData {
  class_index:number;
  density:number;
}

export interface SavedTrainingState {
  is_custom_mode:boolean;
  training_status:TrainingStatus;
  training_started_at:undefined | Date;
  num_training_iterations:number;
  learning_rate:number;

  current_training_times:number;
  current_acc:number;
  current_loss:number;

}

export interface TrainingState extends SavedTrainingState {
  data_flag:string;
}

/**
 * Info about the model itself.
 */
export interface ModelState {
  id?:string;
  model_name:string;
  model_type:undefined | ModelType;
  features:string[]; // Used only in data recognition
  classifications:string[];
  hidden_layers:number[];
  net:undefined | string;
  max:number[];
  min:number[];
}

export interface DataState {
  data:(number | string)[][][];
  valid_rows:number[][];
  should_hightlight:boolean;
  // 
  symbol_index:number[][];
}

export interface AIModelForSave {
  /**
 * Functionality-related data is stored in model_state.
 */
  model_state:ModelState;

  /**
   * Stores non-functionl data.
   * Used only for revoking training parameters when editing a model
   */
  training_state:SavedTrainingState;

  /**
   * data-state
   * Used for data edit
  */
  data_state:DataState;
}

export enum LastStepBeforeSaving {
  Close,
  FinishTraining,
  SaveInTheDataEditPage,
}

export interface KittenAILabProps {
  element:HTMLElement;
  language_data:LanguageDataType;
  cdn_path?:string;
  /**
   * callback to hide the rendered dom in kitten.
   */
  save_and_close:(model:AIModelForSave, lase_step?:LastStepBeforeSaving) => void;
  show_notice?:ShowNoticeFunction;
  show_confirm_dialog?:ShowConfirmDialogFunction;
  report_event?:DataReportFunc;
}
