import { scaleLinear } from 'd3-scale';
import { select } from 'd3-selection';
import { line } from 'd3-shape';

type DataPoint = {
  x:number,
  y:number[],
};

/**
 * A multi-series line chart that allows you to append new data points
 * as data becomes available.
 */
export class LineChart {
  private numLines:number;
  private data:DataPoint[] = [];
  private svg:d3.Selection<SVGElement, {}, HTMLElement, any>;
  private xScale:d3.ScaleLinear<number, number>;
  private yScale:d3.ScaleLinear<number, number>;
  private paths:d3.Selection<SVGPathElement, {}, HTMLElement, any>[];

  private minY = Number.MAX_VALUE;
  private maxY = Number.MIN_VALUE;

  constructor(containerSelector:string, lineColors:string[]) {
    this.numLines = lineColors.length;
    const container = select(containerSelector);
    const node = container.node() as HTMLElement;
    const totalWidth = node.offsetWidth;
    const totalHeight = node.offsetHeight;
    const margin = { top: 2, right: 0, bottom: 2, left: 2 };
    const width = totalWidth - margin.left - margin.right;
    const height = totalHeight - margin.top - margin.bottom;

    this.xScale = scaleLinear()
      .domain([0, 0])
      .range([0, width]);

    this.yScale = scaleLinear()
      .domain([0, 0])
      .range([height, 0]);

    this.svg = container.append('svg')
      .attr('width', width + margin.left + margin.right)
      .attr('height', height + margin.top + margin.bottom)
      .append('g')
      .attr('transform', `translate(${margin.left},${margin.top})`);

    this.paths = new Array(this.numLines);
    for (let i = 0; i < this.numLines; i++) {
      this.paths[i] = this.svg.append('path')
        .attr('class', 'line')
        .style('fill', 'none')
        .style('stroke', lineColors[i])
        .style('stroke-width', '1.5px');
    }
  }

  reset() {
    this.data = [];
    this.redraw();
    this.minY = Number.MAX_VALUE;
    this.maxY = Number.MIN_VALUE;
  }

  addDataPoint(dataPoint:number[]) {
    if (dataPoint.length !== this.numLines) {
      throw Error('Length of dataPoint must equal number of lines');
    }
    dataPoint.forEach((y) => {
      this.minY = Math.min(this.minY, y);
      this.maxY = Math.max(this.maxY, y);
    });

    this.data.push({ x: this.data.length + 1, y: dataPoint });
    this.redraw();
  }

  private redraw() {
    // Adjust the x and y domain.
    this.xScale.domain([1, this.data.length]);
    this.yScale.domain([this.minY, this.maxY]);
    // Adjust all the <path> elements (lines).
    const getPathMap = (lineIndex:number) => line<{x:number, y:number[]}>()
      .x((d) => this.xScale(d.x))
      .y((d) => this.yScale(d.y[lineIndex]));
    for (let i = 0; i < this.numLines; i++) {
      this.paths[i].datum(this.data).attr('d', getPathMap(i));
    }
  }
}