import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { Dispatch, bindActionCreators } from 'redux';
import { connect } from 'react-redux';

import { ChooseTypeDialog } from './dialogs/choose_type_dialog';
import { AILabHeader } from './header';
import { AILabContent } from './content';
import { ModelType, AIModelForSave, LastStepBeforeSaving } from './models/def';
import {
  StoreState,
  reset_store,
  set_ai_lab_visibility,
} from './redux';
import { ConfirmDialog } from './dialogs/notification_dialog';
import { ContextMenuComponent } from './context_menu';
import { NoticeComponent } from './notice';
import { store_manager, machine_learning } from './controllers';
import { set_current_intl } from './i18n';
import { show_confirm_dialog } from './connected_deps';
import { is_model_modified } from './models/utils';
import './main.scss';

interface AILabProps {
  model_id?:string;
  save_and_close:(model:AIModelForSave|undefined, last_step?:LastStepBeforeSaving) => void;
}
interface ConnectedProps extends InjectedIntlProps {
  model_type:ModelType;
  should_show_confirm_dialog:boolean;
  should_show_notice:boolean;
  has_trained:boolean;
  reset_store:typeof reset_store;
  is_visible:boolean;
  set_ai_lab_visibility:typeof set_ai_lab_visibility;
  is_context_menu_show:boolean;
}

/**
 * Main component of ai lab
 */
class AILab extends React.PureComponent<AILabProps & ConnectedProps, {}> {

  constructor(props:AILabProps & ConnectedProps) {
    super(props);
    set_current_intl(this.props.intl);
  }

  UNSAFE_componentWillMount() {
    // When editing a model, require the corresponding model data here, and map to state
    this.init_model_info();
  }

  private async init_model_info() {
    if (!this.props.model_id) {
      return;
    }
    // TODO: If model_id is passed, fetch api to get model info
    // const current_model = await request_model_detail(this.props.model_id);
    return;
  }

  private save_reset_and_close = (save_data:boolean, last_step?:LastStepBeforeSaving) => {
    const callback = (has_data:boolean = false) => {
      machine_learning.stop();
      const model = has_data ? store_manager.get_save_info() : undefined;
      this.props.save_and_close(model, last_step);
      this.props.reset_store();

      //  Ai Lab  redux  state 
      this.props.set_ai_lab_visibility(false);
    };
    if (!save_data) {
      // 
      is_model_modified() ?
        show_confirm_dialog(
          callback,
          'ai_lab/give_up_data',
          this.props.intl.formatMessage({ id: 'ai_lab/give_up_data_content' }),
          { text: 'ai_lab/confirm_give_up', type: 'danger' },
          'cancel',
        ) :
        callback();
      return;
    } else {
      // 
      callback(save_data);
    }
  }

  render() {
    return (
      <div>
        {this.props.is_visible ?
          <div styleName="ai-wrapper">
            {!this.props.model_type &&
              <ChooseTypeDialog save_and_close={this.props.save_and_close} />}
            {this.props.model_type &&
              <div styleName="main-wrapper">
                <AILabHeader save_and_close={this.save_reset_and_close} />
                <AILabContent save_and_close={this.save_reset_and_close} />
                {this.props.should_show_confirm_dialog && <ConfirmDialog />}
              </div>}
            {this.props.should_show_notice && <NoticeComponent />}
            {this.props.is_context_menu_show && <ContextMenuComponent />}
          </div> :
          ''
        }
      </div>
    );
  }
}

function map_state_to_props(state:StoreState, props:AILabProps) {
  return {
    model_type: state.model_state.model_type,
    should_show_confirm_dialog: state.confirm_dialog_state.is_show,
    should_show_notice: state.notice_state.is_show,
    has_trained: !!state.training_state.training_started_at,
    is_visible: state.main_state.is_visible,
    is_context_menu_show: state.context_menu_state.is_show,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      reset_store,
      set_ai_lab_visibility,
    },
    dispatch);
}

export const AILabWrapper =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl(AILab));
