export type NoticeType = 'notice' | 'success' | 'error' | 'progress' | 'loading' | 'custom';

export type ShowNoticeFunction = (
  content:string,
  type?:NoticeType,
) => void;

let props_show_notice:ShowNoticeFunction|null = null;
export function set_props_show_notice(s_n:ShowNoticeFunction) {
  props_show_notice = s_n;
}

export function get_props_show_notice() {
  return props_show_notice;
}

export type ShowConfirmDialogFunction = (callback:Function, title:string, content:string, right:{ text:string, type?:string }, left?:string) => void;

let props_show_confirm_dialog:ShowConfirmDialogFunction|null = null;
export function set_props_show_confirm_dialog(s_c_d:ShowConfirmDialogFunction) {
  props_show_confirm_dialog = s_c_d;
}

export function get_props_show_confirm_dialog() {
  return props_show_confirm_dialog;
}


export type DataReportFunc = (ev:string) => void;

let report_event:DataReportFunc|null = null;
export function set_props_report_event(r_e:DataReportFunc) {
  report_event = r_e;
}

export function get_props_report_event() {
  return report_event;
}
