import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';

import {
  StoreState, choose_step, set_net,
  set_training_status, refresh_training_result,
  set_start_time, set_custom_mode,
  set_num_training_iterations, set_learning_rate,
} from '../../redux';
import { SwitchComponent, DropdownComponent } from '../../components';
import { machine_learning, HeatMap, Example2D, LineChart } from '../../controllers';
import {
  TrainingStep, CLASS_COLORS, TrainingStatus, DrawData,
  SIZE_PREVIEW, ACC_LINE_COLOR, LOSS_LINE_COLOR,
  RANGE_TRAINING_ITERATIONS, RANGE_LEARNING_RATE, LastStepBeforeSaving,
} from '../../models/def';
import { ModelNameComponent } from '../model_name';
import { report_event } from '../../connected_deps';
import { NNDiagramComponent } from './nn_diagram';
import { ResultDemonstratorComponent } from './result_demonstrator';
import { AIModelTestComponent } from './model_test';

import './style.scss';

const WIDTH_PREVIEW = 230;

interface Props extends InjectedIntlProps {
  current_step:TrainingStep;
  choose_step:typeof choose_step;

  training_status:TrainingStatus;
  set_training_status:typeof set_training_status;

  training_started_at:undefined | Date;
  set_start_time:typeof set_start_time;

  is_custom_mode:boolean;
  set_custom_mode:typeof set_custom_mode;
  num_training_iterations:number;
  set_num_training_iterations:typeof set_num_training_iterations;
  learning_rate:number;
  set_learning_rate:typeof set_learning_rate;

  hidden_layers:number[];

  refresh_training_result:typeof refresh_training_result;

  classifications:string[];
  data:(string | number)[][][];
  valid_rows:number[][];

  features:string[];
  set_net:typeof set_net;
  data_flag:string;
}

interface OwnProps {
  save_and_close:(save_data:boolean, last_step?:LastStepBeforeSaving) => void;
}

interface OwnState {
  just_reset:boolean;
}

class Train extends React.PureComponent<Props & OwnProps, OwnState> {
  private heat_map:HeatMap;
  private line_chart:LineChart;

  constructor(props:Props & OwnProps) {
    super(props);

    this.state = {
      just_reset: false,
    };
  }

  private set_custom_mode = (is_custom:boolean) => {
    is_custom ?
      report_event('AI2-') :
      report_event('AI2-');

    this.props.set_custom_mode(is_custom);
  }

  private get_controllers() {
    const sdate = this.props.training_started_at;
    const start_time =
      (sdate && sdate.toLocaleString(undefined, { hour12: false })) || '';

    let training_title = this.props.intl.formatMessage({ id: 'start_to_train' });
    let training_subtitle = start_time;
    let btn_class = 'btn-start';

    if (!this.props.is_custom_mode) { // auto training mode
      if (this.props.training_status === 'training') {
        training_title = this.props.intl.formatMessage({ id: 'traininging' });
        training_subtitle = this.props.intl.formatMessage({ id: 'training_subtitle' });
        btn_class = 'btn-loading-stop';
      }
    } else { // custom mode, cannot stop manually
      switch (this.props.training_status) {
        case 'training':
          training_title = this.props.intl.formatMessage({ id: 'traininging' });
          btn_class = 'btn-loading';
          break;
        case 'stopped':
          training_title = this.props.intl.formatMessage({ id: 'continue_to_train' });
          break;
      }
    }

    // Cannot change custom parameters while training
    const training_style:React.CSSProperties | undefined =
      this.props.training_status === 'training' && { opacity: 0.5, pointerEvents: 'none' } ||
      undefined;

    return (
      <div styleName="training-controller-wrapper">
        <div styleName="start-btn-wrapper">
          {this.props.is_custom_mode && this.props.training_status === 'stopped' &&
            <div
                styleName="btn-restart"
                onClick={this.reset_data}
            >
              <div styleName={`btn-restart-hint ${this.props.intl.locale === 'en' ? 'en' : ''}`}>{ this.props.intl.formatMessage({ id: 'restart_trainging_hint' }) }</div>
            </div>}
          <div
              styleName={btn_class}
              onClick={this.change_training_status}
          />
          <div styleName="start-content">
            <div styleName="training-state">{training_title}</div>
            {training_subtitle && <div styleName="start-time">{training_subtitle}</div>}
          </div>
        </div>
        <div
            styleName="custom-mode-wrapper"
            style={training_style}
        >
          <SwitchComponent
              current_status={this.props.is_custom_mode}
              label={this.props.intl.formatMessage({ id: 'custom_mode' })}
              set_status={this.set_custom_mode}
              hint={this.props.intl.formatMessage({ id: 'custom_mode_hint' })}
          />
          {this.get_training_parameters()}
        </div>
        <div
            styleName="edit_data_btn"
            className="th1-text-0 th1-text-s2 th1-border-impt-1 th1-text-hover-10 th1-bg-hover-0 th1-border-hover-0 th1-bg-active-m2 th1-border-active-m2 th1-text-active-10"
            data-report-click="AI2-"
            onClick={this.props.choose_step.bind(this, TrainingStep.EDITING_DATA)}
        >
          {this.props.intl.formatMessage({ id: 'edit_data' })}
        </div>
      </div>
    );
  }

  private get_training_parameters() {
    return (
      <div styleName="custom-parameters-wrapper">
        <DropdownComponent
            label={this.props.intl.formatMessage({ id: 'epoch' })}
            selections={RANGE_TRAINING_ITERATIONS}
            current_selection={this.props.is_custom_mode ? this.props.num_training_iterations : this.props.intl.formatMessage({ id: 'arbitrary' })}
            choose_callback={this.props.set_num_training_iterations}
            disabled={!this.props.is_custom_mode} />
        <DropdownComponent
            label={this.props.intl.formatMessage({ id: 'learning_rate' })}
            selections={RANGE_LEARNING_RATE}
            current_selection={this.props.learning_rate}
            choose_callback={(learning_rate:number) => {
              this.reset_data();
              this.props.set_learning_rate(learning_rate);
            }}
            disabled={!this.props.is_custom_mode} />
      </div>
    );
  }

  private get_training_previews = () => (
    <div
        styleName="training-preview-wrapper"
        className="th1-bg-9"
    >
      {this.get_input_view()}
      <NNDiagramComponent />
      {this.get_result_view()}
    </div>
  );

  private get_input_view = () => (
    <div styleName="input-view">
      <div styleName="input-title">
        {this.props.intl.formatMessage({ id: 'input_title' })}
      </div>
      <div styleName="input-pic" />
    </div>
  );

  private get_result_view = () => (
    <div styleName="result-view">
      <div styleName="result-title">
        {this.props.intl.formatMessage({ id: 'result_title' })}
      </div>
      <div styleName="result-content">
        <div styleName="result-left">
          <div
              styleName="result-preview"
              id="heatmap"
          >
            {
              this.props.features.length > 2 &&
              <div styleName="feature-notice">
                {
                  this.props.intl.formatMessage({ id: 'features_count_is_more_than_2_and_unable_to_show_result_pic' })
                    .split('|')
                    .map((text) => <div key={text}>{text}</div>)
                }
              </div>
            }
          </div>
          <div styleName="preview-info">
            {this.props.classifications.map((classification, index) => {
              const color_style = { background: CLASS_COLORS[index] };
              const wrap_style = this.props.classifications.length > 2 ? { width: '96px' } : undefined;
              return (
                <div
                    styleName="info-wrapper"
                    style={wrap_style}
                    key={index}
                >
                  <div
                      styleName="color"
                      style={color_style}
                  />
                  <div styleName="name">{classification}</div>
                </div>
              );
            })}
          </div>
        </div>
        <div styleName="result-right">
          <ResultDemonstratorComponent />
          <div
              styleName="numbers-preview"
              id="linechart"
          />
        </div>
      </div>
    </div>
  );

  /** **************************** LOGIC ******************************/
  /**
   * Init all training callback funtions and two diagrams.
   * Sould be called **only once**
   */
  private init_training = () => {
    const report_finish = () => {
      const status = this.state.just_reset ? 'wait_start' : 'stopped';
      this.props.set_training_status(status);
    };
    const report_per_round = (current_training_times:number, current_acc:number, current_loss:number, draw_data:DrawData[][], net:string) => {
      this.heat_map.updateBackground(draw_data);
      this.props.set_net(net);

      this.props.refresh_training_result(
        current_training_times,
        current_acc,
        current_loss,
      );

      if (!isNaN(current_acc) && !isNaN(current_loss)) {
        this.line_chart.addDataPoint([current_acc, current_loss]);
      }
    };
    machine_learning.set_round_callback(report_per_round);
    machine_learning.set_callback_function(report_finish);

    this.heat_map = new HeatMap(WIDTH_PREVIEW, SIZE_PREVIEW, '#heatmap');
    this.line_chart = new LineChart('#linechart', [ACC_LINE_COLOR, LOSS_LINE_COLOR]);
  }

  private get_valid_data = (ori_data:(string|number)[][][], valid_rows:number[][]) => {
    const is_data_in_valid_rows = (class_idx:number, row_idx:number) => valid_rows[class_idx] && valid_rows[class_idx].indexOf(row_idx) >= 0;

    return ori_data
      .map(
        (class_data, class_idx) => class_data.filter((row_data, row_idx) => is_data_in_valid_rows(class_idx, row_idx)),
      );
  }

  /**
   * Reset the data domain, axes and data points of heatmap
   */
  private reset_heatmap(features:string[], valid_data:(string|number)[][][], domains:[number, number][]) {
    if (features.length !== 2) { return; } // supports 2 features only
    const train_data:Example2D[] = [];
    valid_data.forEach((classification_data, class_index) => {
      classification_data.forEach((data_row) => {
        train_data.push({ x: Number(data_row[0]), y: Number(data_row[1]), label: class_index });
      });
    });

    this.heat_map.resetPoints(domains[0], domains[1], train_data, features);
  }

  /**
   * Things happen when click control btn
   */
  private change_training_status = () => {
    if (!this.props.is_custom_mode) { // auto training mode
      if (this.props.training_status === 'training') {
        report_event('AI2-');
        machine_learning.stop(false);
      } else { // wait_start or stopped
        // heatmap
        report_event('AI2-');
        this.start_training(!!this.props.training_started_at);
      }
    } else { // custom mode, cannot stop manually
      if (this.props.training_status === 'wait_start' || this.state.just_reset) {
        this.start_training(true);
      } else if (this.props.training_status !== 'training') {
        this.start_training(false);
      }
    }
  }

  /**
   * Start a train.
   * When required (should_reset === true), reset the status of machine learning class, including history accuracy and loss, net, etc.
   * If no training has been started at this time of use, should_reset should be true to init related parameters.
   */
  private start_training = (should_reset:boolean) => {
    this.setState({
      just_reset: false,
    });
    const current_date = new Date();
    this.props.set_training_status('training');
    this.props.set_start_time(current_date);

    if (should_reset) {
      const valid_data = this.get_valid_data(this.props.data, this.props.valid_rows);
      const new_domains = machine_learning.set_training_data(valid_data, this.props.features.length);
      this.reset_heatmap(this.props.features, valid_data, new_domains);
      this.line_chart.reset();
    }

    machine_learning.set_trainer_params(
      this.props.is_custom_mode,
      this.props.num_training_iterations,
      this.props.learning_rate,
    );
    machine_learning.start(should_reset);
  }

  private reset_data = () => {
    this.line_chart.reset();
    machine_learning.reset_data();
    this.setState(
      {
        just_reset: true,
      },
      () => {
        this.props.set_training_status('wait_start');
      },
    );
  }

  private get_training_footer = () => {
    const is_stopped = this.props.training_status === 'stopped';

    return (
      <div styleName="content-footer">
        <div styleName="tips">
          {this.props.intl.formatMessage({ id: 'training_tips' })}
        </div>
        <div styleName="btn-group">
          <ModelNameComponent />
          {
            <div
                styleName={is_stopped ? 'btn-positive' : 'btn-negative'}
                className={is_stopped ? 'th1-bg-0' : ''}
                onClick={
                  is_stopped ?
                    () => {
                      report_event('AI2-');
                      this.props.save_and_close(true, LastStepBeforeSaving.FinishTraining);
                    } :
                    undefined
                }
            >
              {this.props.intl.formatMessage({ id: 'complete' })}
            </div>
          }
        </div>
      </div>
    );
  };

  /** **************************** REACT LIFTCYCLE ***************************/

  UNSAFE_componentWillReceiveProps(next_props:Props) {
    if (next_props.current_step === TrainingStep.TRAINING &&
        next_props.hidden_layers !== this.props.hidden_layers) {
      machine_learning.set_hidden_layers(this.props.hidden_layers);
      // Real-time refresh heatmap when changing hidden layers if no train has been performed
      if (!this.props.training_started_at) {
        machine_learning.preview();
      }
      machine_learning.stop();
      this.props.set_training_status('wait_start');
    }

    // // If not trained yet, reset the preview diagram when entering training step
    // if (this.props.current_step !== TrainingStep.TRAINING && next_props.current_step === TrainingStep.TRAINING) {
    //   const should_preview = !this.props.training_started_at;
    //   const valid_data = this.get_valid_data(next_props.data, next_props.valid_rows);
    //   const new_domains = machine_learning.set_training_data(valid_data, next_props.features.length);
    //   this.reset_heatmap(next_props.features, valid_data, new_domains);
    //   if (should_preview) {
    //     machine_learning.preview();
    //   }
    // }

    // 
    if (
      this.props.data_flag !== next_props.data_flag
    ) {
      // 
      this.heat_map.destroy();
      this.heat_map = new HeatMap(WIDTH_PREVIEW, SIZE_PREVIEW, '#heatmap');

      // 
      const valid_data = this.get_valid_data(next_props.data, next_props.valid_rows);
      const new_domains = machine_learning.set_training_data(valid_data, next_props.features.length);
      this.reset_heatmap(next_props.features, valid_data, new_domains);

      // 
      machine_learning.stop();
      this.reset_data();
      machine_learning.preview();
    }
  }

  componentDidMount() {
    this.init_training();
  }

  render() {
    const is_show_test_area = this.props.training_status === 'stopped' && !this.state.just_reset;

    return (
      <div styleName="main-content-wrapper">
        {this.get_controllers()}
        {this.get_training_previews()}
        {this.get_training_footer()}
        {is_show_test_area && <AIModelTestComponent />}
        {/* FIXME should not show when the model is reset (by editing data or hidden layers organization) */}
      </div>
    );
  }
}

function map_state_to_props(state:StoreState, props:OwnProps) {
  return {
    current_step: state.main_state.current_step,
    classifications: state.model_state.classifications,

    training_status: state.training_state.training_status,
    training_started_at: state.training_state.training_started_at,

    is_custom_mode: state.training_state.is_custom_mode,
    num_training_iterations: state.training_state.num_training_iterations,
    learning_rate: state.training_state.learning_rate,
    hidden_layers: state.model_state.hidden_layers,

    data: state.data_state.data,
    valid_rows: state.data_state.valid_rows,
    features: state.model_state.features,
    data_flag: state.training_state.data_flag,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      choose_step,
      set_training_status,
      refresh_training_result,
      set_start_time,
      set_net,
      set_custom_mode,
      set_num_training_iterations,
      set_learning_rate,
    },
    dispatch);
}

export const TrainComponent =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl((Train)));
