import * as React from 'react';
import * as xlsx from 'xlsx';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { groupBy, cloneDeep, uniqueId } from 'lodash';

import {
  StoreState,
  change_data,
  change_classification,
  choose_step,
  change_feature,
  set_training_status,
  reset_data_flag,
  Theme,
} from '../../redux';
import { RANGE_NUM_FEATURES, DataSet, TrainingStep, RANGE_NUM_CLASSES, DEFAULT_DATA_LENGTH } from '../../models/def';
import { get_validated_name, rename_base_on_range } from '../../models/utils';
import { show_notice, show_confirm_dialog, get_dynamic_theme_class, report_event } from '../../connected_deps';

import './style.scss';

interface ConnectedProps extends InjectedIntlProps {
  change_data:typeof change_data;
  change_classification:typeof change_classification;

  change_feature:typeof change_feature;

  source_input:File | DataSet;
  choose_step:typeof choose_step;
  set_training_status:typeof set_training_status;
  reset_data_flag:typeof reset_data_flag;
  theme:Theme;
}

interface DataState {
  rows:(string | number)[][];
  header_index_to_show_dropdown_menu?:number;
  /**
   * Indicate the column index of data being marked as classification column.
   * Also refresh when drop event happen, i.e. the values are not always correct
   * **Should be used only when clicking confirm to add.**
   */
  column_of_classification?:number;
  features:DraggableFeature[];
  should_show_hint:boolean;
}

interface DraggableFeature {
  name:string;
  dropped_on:number;
  id:string;
}

class ImportData extends React.Component<ConnectedProps, DataState> {
  constructor(props:ConnectedProps) {
    super(props);
    this.state = {
      rows: [['']],
      features: [],
      should_show_hint: false,
    };
    if (props.source_input instanceof File) {
      const import_data = props.source_input;
      // .csv  readAsText 
      const is_csv = import_data.type === 'text/csv';

      this.last_step = TrainingStep.EDITING_DATA;
      const reader = new FileReader();
      reader.onload = (ev:any) => {
        const data = ev.target.result;
        const type = is_csv ? 'string' : 'binary';

        try {
          const workbook = xlsx.read(data, { type });
          this.sheet_to_arr(workbook.Sheets[workbook.SheetNames[0]]);
        } catch (err) {
          show_notice({
            id: 'ai_lab/invalid_file_data',
            type: 'error',
          });
        }
      };
      is_csv ? reader.readAsText(import_data, 'utf-8') : reader.readAsBinaryString(import_data);
    } else {
      this.last_step = TrainingStep.CHOOSING_DATA_SET;
      this.state = { ...this.state, rows: props.source_input.source };
    }
  }

  componentDidMount() {
    document.addEventListener('mousedown', this.handle_click_dropdown_ouside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handle_click_dropdown_ouside);
  }

  private last_step:TrainingStep;

  //  input  onFocus 
  // 
  private last_name:string;
  // 
  private name_list:string[];

  private sheet_to_arr(sheet:xlsx.WorkSheet) {
    const ref:string = sheet['!ref'] ? sheet['!ref'] : '';
    const range = xlsx.utils.decode_range(ref);
    const rows:string[][] = [];

    const is_column_valid:boolean[] = [];

    for (let row_num = range.s.r; row_num <= range.e.r; row_num++) {
      const row = [];
      for (let col_num = range.s.c; col_num <= range.e.c; col_num++) {
        if (row_num === 0) {
          is_column_valid[col_num] = false;
        }
        const current_cell = sheet[xlsx.utils.encode_cell({ r: row_num, c: col_num })];
        if (current_cell) {
          row.push(current_cell.v);
          is_column_valid[col_num] = true;
        } else {
          row.push('');
        }
      }
      rows.push(row);
    }

    // Ignore the empty rows when adding data to the table
    const res_rows = rows.map((row) => row.filter((col, col_index) => is_column_valid[col_index]));
    this.setState({ rows: res_rows });
  }

  private handle_add_data = () => {
    report_event('AI2-');

    const { column_of_classification } = this.state;
    if (column_of_classification === undefined) {
      return;
    }

    const grouped_rows = groupBy(this.state.rows, (row) => row[column_of_classification]);
    const classifications = Object.keys(grouped_rows).filter((key) => key !== 'undefined' && key !== '');
    let filtered = false;
    const column_map_to_feature = this.state.features.map((feature) => feature.dropped_on);
    const data = classifications.map((classification) => {
      const class_data = grouped_rows[classification].map((row) => column_map_to_feature.map((i) => row[i]));
      const res_data = class_data.filter((data_row) => data_row.every((data_cell) => !isNaN(parseFloat(data_cell.toString()))));
      if (res_data.length < class_data.length) {
        filtered = true;
      }
      return res_data;
    });

    // After data filtering, check if all the classifications contain data.
    // remove the empty classifications
    // TODO confirm the necessarity
    for (let i = classifications.length - 1; i >= 0; i--) {
      const feature_rows = data[i];
      const feature_row_len = feature_rows.length;

      if (feature_row_len < 1) {
        filtered = true;
        classifications.splice(i, 1);
        data.splice(i, 1);
      } else if (feature_row_len < DEFAULT_DATA_LENGTH) {
        // 
        const rows_remain = DEFAULT_DATA_LENGTH - feature_row_len;

        for (let j = 0; j < rows_remain; j++) {
          feature_rows.push(Array(this.state.features.length).fill(''));
        }
      }
    }

    if (classifications.length === 0 || classifications.length > RANGE_NUM_CLASSES[1] ) {
      const title_id = classifications.length ? 'ai_lab/add_data_fail_due_to_classification_maxium' : 'cannot_add_data';
      const message_id = classifications.length ? 'max_feature_length_exceed' : 'import_file_contains_no_legal_data';

      show_confirm_dialog(
        () => { this; },
        title_id,
        this.props.intl.formatMessage({ id: message_id }),
        { text: 'ai_lab/cannot_add_data_confirm' },
      );
      return;
    }

    //  classification 
    const extra_default_classification:string[] = [];
    if (classifications.length < RANGE_NUM_CLASSES[0]) {
      const classifications_remain = RANGE_NUM_CLASSES[0] - classifications.length;

      for (let i = 0; i < classifications_remain; i++) {
        const new_classification = rename_base_on_range(this.props.intl.formatMessage({ id: 'classification' }), classifications, RANGE_NUM_CLASSES[1]);
        classifications.push(new_classification);
        extra_default_classification.push(new_classification);

        // 
        const rows = [];
        for (let j = 0; j < DEFAULT_DATA_LENGTH; j++) {
          rows.push(Array(this.state.features.length).fill(''));
        }
        data.push(rows);
      }
    }

    const import_data = () => {
      const features = this.state.features.map((feature) => feature.name);
      this.props.change_feature(features);
      this.props.change_classification(classifications);
      this.props.change_data(data);
      if (filtered) {
        show_notice({
          id: 'invalid_data_deleted',
        });
      }
      this.props.choose_step(TrainingStep.EDITING_DATA);
      this.props.set_training_status('wait_start');
      this.props.reset_data_flag();
    };

    show_confirm_dialog(
      import_data,
      'add_data',
      this.props.intl.formatMessage({ id: 'adding_classes' }, { classifications: ` ${classifications.filter((c) => !extra_default_classification.includes(c)).join(', ')}` }),
      { text: 'confirm' },
      'cancel',
    );
  }

  private handle_feature_add = (dropped_on:number) => {
    if (this.state.features.length >= RANGE_NUM_FEATURES[1]) {
      return;
    }

    const features = cloneDeep(this.state.features);
    const feature_name_arr = features.map((feature) => feature.name);
    const default_feature_name_prefix = this.props.intl.formatMessage({ id: 'feature' });

    features.push({
      name: rename_base_on_range(default_feature_name_prefix, feature_name_arr, RANGE_NUM_FEATURES[1]),
      dropped_on,
      id: uniqueId(),
    });

    this.setState({
      features,
    });
  }

  private get_feature_index_by_id = (id:string) => this.state.features.findIndex((feature) => feature.id === id)

  private handle_feature_del = (id:string) => {
    const features = cloneDeep(this.state.features);
    const del_feature_idx = this.get_feature_index_by_id(id);
    if (del_feature_idx === -1) {
      return;
    }

    features.splice(del_feature_idx, 1);
    this.setState({
      features,
    });
  }

  private handle_feature_name_edit = (id:string, new_name:string) => {
    const features = cloneDeep(this.state.features);
    const edit_feature_idx = this.get_feature_index_by_id(id);
    if (edit_feature_idx === -1) {
      return;
    }

    features[edit_feature_idx].name = new_name;
    this.setState({
      features,
    });
  }

  private handle_feature_name_edit_on_blur = (id:string, last_name:string, new_name:string) => {
    const features = cloneDeep(this.state.features);

    const edit_feature_idx = this.get_feature_index_by_id(id);
    if (edit_feature_idx === -1) {
      return;
    }

    new_name = get_validated_name(new_name, last_name, this.name_list);

    features[edit_feature_idx].name = new_name;
    this.setState({
      features,
    });
  }

  private render_header = () => {
    const should_add_data = this.state.features.length > 0 && this.state.column_of_classification !== undefined;

    return (
      <div styleName="header">
        <div
            styleName="cancel_btn"
            className="th1-text-0 th1-text-s2"
            onClick={this.props.choose_step.bind(this, this.last_step)}
        >
          <div styleName="icon">
            <svg
                className="icon"
                aria-hidden="true"
            >
              <use xlinkHref="#iconicon-dropdown" />
            </svg>
          </div>
          <div styleName="text">
            {this.props.intl.formatMessage({ id: 'cancel_add' })}
          </div>
        </div>
        <div
            styleName={`confirm_btn ${should_add_data ? 'positive' : 'negative'}`}
            className={should_add_data ? 'th1-bg-0 th1-bg-hover-m1 th1-bg-active-m2 th1-text-10' : ''}
            onClick={() => {
              should_add_data && this.handle_add_data();
            }}
            onMouseEnter={() => {
              if (should_add_data) {
                return;
              }

              this.setState({
                should_show_hint: true,
              });
            }}
            onMouseLeave={() => {
              if (should_add_data) {
                return;
              }

              this.setState({
                should_show_hint: false,
              });
            }}
        >
          {this.props.intl.formatMessage({ id: 'confirm_add' })}
          {
            !should_add_data &&
            <div
                styleName="disable_hint"
            >
              <svg
                  className="icon"
                  aria-hidden="true"
              >
                <use xlinkHref="#iconicon_jinggao" />
              </svg>
            </div>
          }
          {
            this.state.should_show_hint &&
            <div styleName="hint_label">
              {this.props.intl.formatMessage({ id: 'ai_lab/at_least_1_feature_and_1_classification' })}
            </div>
          }
        </div>
      </div>
    );
  }

  private handle_click_dropdown_ouside = (e:MouseEvent) => {
    if (!this.dropdown_div) {
      return;
    }

    const el = e.target as HTMLElement;
    if (this.dropdown_div.contains(el)) {
      return;
    }

    this.setState({
      header_index_to_show_dropdown_menu: undefined,
    });
  }

  private dropdown_div:HTMLDivElement|null;

  private inputs:(HTMLInputElement|null)[] = [];

  private render_table_header = () => {
    const { features, column_of_classification, rows } = this.state;

    const dimension = rows[0].length;
    const table_headers:JSX.Element[] = [];

    for (let i = 0; i < dimension; i++) {
      const is_selected_feature = features.some((f) => f.dropped_on === i);
      const is_selected_classfication = column_of_classification === i;

      table_headers.push(
        <th
            key={i}
            className={
              `th1-border-impt-7
              ${get_dynamic_theme_class('th1-bg-6', this.props.theme)}`
            }
        >
          <div styleName="item_container">
            <div styleName="input">
              {
                !is_selected_feature && !is_selected_classfication &&
                <div
                    styleName="unselected"
                    className={get_dynamic_theme_class('th1-text-3', this.props.theme, 4)}
                >
                  {this.props.intl.formatMessage({ id: 'ai_lab/unselected' })}
                </div>
              }
              {
                is_selected_feature && (() => {
                  const found = features.find((f) => f.dropped_on === i);
                  if (!found) {
                    return false;
                  }

                  return (
                    <input
                        className="th1-text-0 th1-text-s2 th1-border-impt-0"
                        type="text"
                        value={found.name}
                        onFocus={
                          (e) => {
                            e.target.select();
                            this.last_name = found.name;
                            this.name_list = features.map((f) => f.name);
                          }
                        }
                        maxLength={10}
                        onChange={
                          (e) => {
                            this.handle_feature_name_edit(found.id, e.target.value);
                          }
                        }
                        onBlur={
                          (e) => {
                            this.handle_feature_name_edit_on_blur(found.id, this.last_name, e.target.value);
                          }
                        }
                        ref={(ref) => {
                          this.inputs[i] = ref;
                        }}
                        onKeyDown={
                          (e) => {
                            const input = this.inputs[i];
                            e.keyCode === 13 && input && input.blur();
                          }
                        }
                    />
                  );
                })()
              }
              {
                is_selected_classfication &&
                <div
                    styleName="selected"
                    className="th1-text-0 th1-text-s2"
                >
                  {this.props.intl.formatMessage({ id: 'classification' })}
                </div>
              }
            </div>
            <div
                styleName={`dropdown ${this.state.header_index_to_show_dropdown_menu === i ? 'expand' : ''}`}
                className="th1-text-0 th1-text-s2 th1-bg-hover-4 th1-bg-5"
                onClick={() => {
                  this.setState((prev_state) => {
                    const header_index_to_show_dropdown_menu = prev_state.header_index_to_show_dropdown_menu === i ? undefined : i;
                    return {
                      header_index_to_show_dropdown_menu,
                    };
                  });
                }}
            >
              <svg
                  className="icon"
                  aria-hidden="true"
              >
                <use xlinkHref="#iconicon_down1" />
              </svg>
            </div>
            {
              this.state.header_index_to_show_dropdown_menu === i &&
              <div
                  styleName="dropdown_menu"
                  ref={(ref) => {
                    this.dropdown_div = ref;
                  }}
              >
                {
                  [
                    {
                      type: 'item',
                      text: 'feature',
                      disabled: features.length >= RANGE_NUM_FEATURES[1] && !is_selected_feature,
                      selected: is_selected_feature,
                      data_report: 'AI2-',
                      cb: () => {
                        if (features.length >= RANGE_NUM_FEATURES[1]) {
                          show_notice({
                            id: 'ai_lab/feature_maxmiun',
                          });
                          return false;
                        }

                        if (is_selected_feature) {
                          return true;
                        }

                        if (is_selected_classfication) {
                          this.setState({
                            column_of_classification: undefined,
                          });
                        }

                        this.handle_feature_add(i);
                        return true;
                      },
                    },
                    {
                      type: 'item',
                      text: 'classification',
                      disabled: this.state.column_of_classification !== undefined && !is_selected_classfication,
                      selected: is_selected_classfication,
                      data_report: 'AI2-',
                      cb: () => {
                        if (is_selected_classfication) {
                          return true;
                        }

                        if (this.state.column_of_classification !== undefined) {
                          show_notice({
                            id: 'ai_lab/classification_maxmiun',
                          });
                          return false;
                        }

                        if (is_selected_feature) {
                          const found = features.find((f) => f.dropped_on === i);
                          if (!found) {
                            return false;
                          }

                          this.handle_feature_del(found.id);
                        }

                        this.setState({
                          column_of_classification: i,
                        });

                        return true;
                      },
                    },
                    {
                      type: 'divider',
                      text: 'divider',
                      hide: !is_selected_feature && !is_selected_classfication,
                    },
                    {
                      type: 'item',
                      text: 'ai_lab/clear_selection',
                      hide: !is_selected_feature && !is_selected_classfication,
                      data_report: 'AI2-',
                      cb: () => {
                        if (is_selected_classfication) {
                          this.setState({
                            column_of_classification: undefined,
                          });
                          return true;
                        }

                        const found = features.find((f) => f.dropped_on === i);
                        if (!found) {
                          return false;
                        }

                        this.handle_feature_del(found.id);
                        return true;
                      },
                    },
                  ].map(({ type, text, cb, disabled, hide, selected, data_report }) => (
                    type === 'item' ?
                      <div
                          key={text}
                          styleName={`menu_item ${disabled ? 'disabled' : ''}`}
                          style={
                            hide ?
                              { display: 'none' } :
                              {}
                          }
                          className={`${disabled ? '' : 'th1-bg-hover-8'}`}
                          data-report-click={data_report ? data_report : ''}
                          onClick={
                            () => {
                              const should_close_dropdown = cb && cb();
                              should_close_dropdown && this.setState({
                                header_index_to_show_dropdown_menu: undefined,
                              });
                            }
                          }
                      >
                        <div styleName="status">
                          {
                            selected &&
                            <div
                                styleName="selected"
                                className="th1-bg-0"
                            />
                          }
                        </div>
                        <div styleName="text">
                          {this.props.intl.formatMessage({ id: text })}
                        </div>
                      </div> :
                      <div
                          key={text}
                          styleName="divider"
                          style={
                            hide ?
                              { display: 'none' } :
                              {}
                          }
                      />
                  ))
                }
              </div>
            }
          </div>
        </th>,
      );
    }

    return (
      <div
          styleName="table_header"
          className="th1-bg-6"
      >
        <table>
          <thead><tr>{table_headers}</tr></thead>
        </table>
      </div>
    );
  }

  private render_table_content = () => {
    const data_rows:JSX.Element[] = [];
    if (this.state.rows && this.state.rows[0]) {
      this.state.rows.forEach((row, row_index) => {
        const is_even_row = row_index % 2 === 0;
        data_rows.push(
          <tr key={row_index}>
            {
              row.map((data_cell, column_index) =>
                <td
                    key={column_index}
                    className={`${is_even_row ? 'th1-bg-8' : 'th1-bg-9'} th1-border-impt-7`}
                >
                  {data_cell}
                </td>)
            }
          </tr>,
        );
      });
    }

    return (
      <div styleName="scrollable-body">
        <table>
          <tbody>{data_rows}</tbody>
        </table>
      </div>
    );
  }

  private render_table = () => {
    if (!(this.state.rows && this.state.rows[0])) { return; }

    return (
      <div styleName="table_container">
        <div
            styleName="title"
            className="th1-bg-0 th1-text-10"
        >
          {this.props.source_input && this.props.source_input.name}
        </div>
        {this.render_table_header()}
        {this.render_table_content()}
      </div>
    );
  }

  private render_content = () => (
    <div
        styleName="content"
        className="th1-bg-9 th1-border-impt-7"
    >
      {this.render_table()}
    </div>
  );

  render() {
    return (
      <div styleName="main-content-wrapper">
        {this.render_header()}
        {this.render_content()}
      </div>
    );
  }
}

function map_state_to_props(state:StoreState) {
  return {
    source_input: state.main_state.chosen_data_set,
    theme: state.main_state.theme,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      change_data,
      change_classification,
      change_feature,
      choose_step,
      set_training_status,
      reset_data_flag,
    },
    dispatch);
}

export const ImportDataComponent =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl((ImportData)));
