import { addLocaleData, InjectedIntl } from 'react-intl';
import * as zh from 'react-intl/locale-data/zh';
import * as en from 'react-intl/locale-data/en';
import * as fr from 'react-intl/locale-data/fr';
import { zh_CN } from './zh_CN';

addLocaleData([...zh, ...en, ...fr]);

let INTL:InjectedIntl;
export function set_current_intl(current_intl:InjectedIntl) {
  INTL = current_intl;
}

export function intl() {
  return INTL;
}

export interface MsgType {
  [id:string]:string;
}

export interface LanguageDataType {
  lang:string;
  messages:MsgType;
}

let locale:string = 'zh';
let ai_lab_messages:MsgType = zh_CN;

export function set_language(language_data:LanguageDataType) {
  const { lang, messages } = language_data;
  switch (lang) {
    case 'tw':
      locale = 'zh-Hant';
      break;
    default:
      locale = lang;
  }
  ai_lab_messages = messages;
}

export function get_locale_data() {
  return {
    locale,
    messages: ai_lab_messages,
  };
}
