import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { connect } from 'react-redux';


import { ModelType, model_type_dict, TrainingStep, LastStepBeforeSaving } from '../models/def';
import { StoreState } from '../redux';
import './style.scss';

interface Props extends InjectedIntlProps {
  current_step:TrainingStep;
  model_type:ModelType;
}

interface States {
  name:string;
}

interface OwnProps {
  save_and_close:(save_data:boolean, last_step?:LastStepBeforeSaving) => void;
}

class LabHeader extends React.Component<OwnProps & Props, States> {
  constructor(props:OwnProps & Props) {
    super(props);
  }

  private get_intl = (id:string) => this.props.intl.formatMessage({ id });

  private get_step_text = (step:TrainingStep, with_arrow:boolean = false) => {
    const text_map = {
      [TrainingStep.EDITING_DATA]: this.props.intl.formatMessage({ id: 'ai_lab/model_type_prefix' }, { model_type: this.get_intl(model_type_dict[this.props.model_type]) }),
      [TrainingStep.TRAINING]: this.get_intl('train'),
      [TrainingStep.CHOOSING_DATA_SET]: this.get_intl('ai_lab/select_data_set'),
      [TrainingStep.IMPORTING]: this.get_intl('ai_lab/edit_data_set'),
    };

    return (
      <React.Fragment
          key={step}
      >
        <div
            styleName={this.props.current_step === step ? 'current-step' : 'other-step'}
        >
          {text_map[step]}
        </div>
        {
          with_arrow &&
          <div
              styleName="icon_wrapper"
          >
            <div
                styleName="icon_arrow"
            >
              <svg
                  className="icon"
                  aria-hidden="true"
              >
                <use xlinkHref="#iconicon-dropdown" />
              </svg>
            </div>
          </div>
        }
      </React.Fragment>
    );
  }

  private get_steps() {
    const {
      EDITING_DATA,
      TRAINING,
      IMPORTING,
      CHOOSING_DATA_SET,
    } = TrainingStep;

    const map:{[key in TrainingStep]:TrainingStep[]} = {
      [EDITING_DATA]: [EDITING_DATA],
      [TRAINING]: [EDITING_DATA, TRAINING],
      [CHOOSING_DATA_SET]: [EDITING_DATA, CHOOSING_DATA_SET],
      [IMPORTING]: [EDITING_DATA, CHOOSING_DATA_SET, IMPORTING],
    };

    return map[this.props.current_step].map((step:TrainingStep, index:number, arr:TrainingStep[]) => this.get_step_text(step, index !== arr.length - 1));
  }

  private handle_help_btn_click = () => {
    window.open('https://codemao.yuque.com/books/share/165bda28-1103-48dd-a9cf-ca11b75ab2a8/cr6yy2');
  }

  private handle_close_btn_click = () => {
    this.props.save_and_close(false, LastStepBeforeSaving.Close);
  }

  render() {
    return (
      <div styleName="header-wrapper">
        <div styleName="dialog-detail">
          {this.get_steps()}
        </div>
        <div styleName="btn_group">
          <div
              styleName="btn_help"
              data-report-click="AI2-"
              onClick={this.handle_help_btn_click}
          >
            <svg
                className="icon"
                aria-hidden="true"
            >
              <use xlinkHref="#iconicon_bangzhu" />
            </svg>
          </div>
          <div
              styleName="btn_close"
              onClick={this.handle_close_btn_click}
          >
            <svg
                className="icon"
                aria-hidden="true"
            >
              <use xlinkHref="#iconicon-delete" />
            </svg>
          </div>
        </div>
      </div>
    );
  }
}

function map_state_to_props(state:StoreState, props:OwnProps) {
  return {
    current_step: state.main_state.current_step,
    model_type: state.model_state.model_type,
  };
}

export const AILabHeader =
  connect(map_state_to_props, {})(injectIntl((LabHeader)));
