import * as React from 'react';
import { connect } from 'react-redux';

import { StoreState } from '../redux';
import { TrainingStep, TrainingStatus, LastStepBeforeSaving } from '../models/def';
import { EditDataComponent } from './edit_data';
import { DataSetComponent } from './data_set_library';
import { ImportDataComponent } from './import_data';
import { TrainComponent } from './train';

import './common.scss';

interface ContentProps {
  current_step:TrainingStep;
  training_status:TrainingStatus;
}

interface OwnProps {
  save_and_close:(save_data:boolean, last_step?:LastStepBeforeSaving) => void;
}

class LabContent extends React.PureComponent<ContentProps & OwnProps, {}> {

  render() {
    return (
      <div
          styleName="content-wrapper"
          id="ai_lab_content"
      >
        <TrainComponent save_and_close={this.props.save_and_close}/>
        {this.props.current_step === TrainingStep.EDITING_DATA && <EditDataComponent save_and_close={this.props.save_and_close}/>}
        {this.props.current_step === TrainingStep.CHOOSING_DATA_SET && <DataSetComponent />}
        {this.props.current_step === TrainingStep.IMPORTING && <ImportDataComponent />}
      </div>
    );
  }
}

function map_state_to_props(state:StoreState, own_pros:OwnProps) {
  return {
    current_step: state.main_state.current_step,
    training_status: state.training_state.training_status,
  };
}

export const AILabContent =
  connect(map_state_to_props, {})((LabContent));