import { TrainingStep, DataSet } from '../../models/def';

export type HSB = [number, number, number];

export interface Theme {
  id:string;
  primary_color:[number, number, number]; // HSB
  second_color:[number, number, number]; // HSB
  light_theme_colors?:{ [id:string]:string }; // 
  extra_color:{ // 16cn
    c:HSB,
    n1:HSB,
    n2:HSB,
  };
}
export interface MainState {
  current_step:TrainingStep;
  chosen_data_set:File | DataSet | undefined;
  should_show_feature_add_notice:boolean;
  is_visible:boolean;
  theme:Theme;
}

export const initial_main_state:MainState = {
  current_step: TrainingStep.EDITING_DATA,
  chosen_data_set: undefined,
  should_show_feature_add_notice: true,
  is_visible: false,
  theme: {
    id: 'theme/blue',
    primary_color: [216, 84, 100],
    second_color: [22, 84, 100],
    extra_color: {
      c: [30, 90, 100],
      n1: [204, 84, 100],
      n2: [134, 69, 84],
    },
  },
};

// Actions
export const CHOOSE_STEP = 'ai_lab/choose_step';
export function choose_step(next_step:TrainingStep) {
  return { type: CHOOSE_STEP, payload: { next_step } };
}

export const CHOOSE_DATA_SET = 'ai_lab/choose_data_set';
export function choose_data_set(data_set:File | DataSet) {
  return { type: CHOOSE_DATA_SET, payload: { data_set } };
}

export const RESET_MAIN_STATE = 'ai_lab/reset_main_state';
export function reset_main_state() {
  return { type: RESET_MAIN_STATE };
}

export const SET_FEATURE_NOTICED = 'ai_lab/set_feature_noticed';
export function set_feature_noticed() {
  return { type: SET_FEATURE_NOTICED };
}

export const SET_THEME = 'ai_lab/set_theme';
export function set_theme(theme:Theme) {
  return { type: SET_THEME, payload: { theme } };
}

export const SET_AI_LAB_VISIBILITY = 'ai_lab/set_ai_lab_visibility';
export function set_ai_lab_visibility(is_visible:boolean) {
  return { type: SET_AI_LAB_VISIBILITY, payload: { is_visible } };
}

// Reducer
export function main_reducer(state = initial_main_state, action:any) {
  switch (action.type) {
    case CHOOSE_STEP:
      return Object.assign({}, state, { current_step: action.payload.next_step });
    case CHOOSE_DATA_SET:
      return Object.assign({}, state, { chosen_data_set: action.payload.data_set });
    case RESET_MAIN_STATE:
      return initial_main_state;
    case SET_FEATURE_NOTICED:
      return Object.assign({}, state, { should_show_feature_add_notice: false });
    case SET_THEME:
      return Object.assign({}, state, { theme: action.payload.theme });
    case SET_AI_LAB_VISIBILITY:
      return Object.assign({}, state, { is_visible: action.payload.is_visible });
    default:
      return state;
  }
}