export interface NoticeState {
  content:string;
  timeout:number;
  is_show:boolean;
}

export const initial_notice_state:NoticeState = {
  content: '',
  timeout: 2000,
  is_show: false,
};

// Actions
export const SHOW_NOTICE = 'ai_lab/show_notice';
export function show_notice(content:string, timeout:number = 2000) {
  return { type: SHOW_NOTICE, payload: { content, timeout } };
}

export const HIDE_NOTICE = 'ai_lab/hide_notice';
export function hide_notice() {
  return { type: HIDE_NOTICE };
}

export function notice_reducer(state = initial_notice_state, action:any) {
  switch (action.type) {
    case SHOW_NOTICE:
      return {
        is_show: true,
        content: action.payload.content,
        timeout: action.payload.timeout,
      };
    case HIDE_NOTICE:
      return initial_notice_state;
    default:
      return state;
  }
}