import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { StoreState } from '../redux';
import { hide_context_menu } from '../redux/reducers/contextmenu_reducer';

import './style.scss';

type Props = InjectedIntlProps & ReturnType<typeof map_state_to_props> & ReturnType<typeof map_dispatch_to_props>

class ContextMenu extends React.Component<Props, {}> {

  constructor(props:Props) {
    super(props);
  }

  componentDidMount() {
    if (!this.context_menu_div) {
      return;
    }

    document.addEventListener('mousedown', this.handle_mouse_down_outside);
  }

  componentWillUnmount() {
    document.removeEventListener('mousedown', this.handle_mouse_down_outside);
  }

  private handle_mouse_down_outside = (e:MouseEvent) => {
    const el = e.target as HTMLElement;
    if (!el) {
      return;
    }

    if (this.context_menu_div && !this.context_menu_div.contains(el)) {
      this.props.hide_context_menu();
    }
  }

  private context_menu_div:HTMLDivElement|null;

  render() {
    return (
      <div
          style={{
            left: this.props.x,
            top: this.props.y,
          }}
          styleName="context_menu_container"
          ref={
            (ref) => {
              this.context_menu_div = ref;
            }
          }
      >
        {
          this.props.menu_items.map(({ text, cb, disabled }) => (
            <div
                className="th1-bg-hover-8"
                key={text}
                styleName={`item ${disabled ? 'disabled' : ''}`}
                onClick={(e) => {
                  cb(e);
                  this.props.hide_context_menu();
                }}
            >
              {this.props.intl.formatMessage({ id: text })}
            </div>
          ))
        }
      </div>
    );
  }
}

function map_state_to_props(state:StoreState) {
  return {
    x: state.context_menu_state.x,
    y: state.context_menu_state.y,
    menu_items: state.context_menu_state.menu_items,
    is_show: state.context_menu_state.is_show,
  };
}
function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      hide_context_menu,
    },
    dispatch);
}

export const ContextMenuComponent =
  connect(
    map_state_to_props,
    map_dispatch_to_props,
  )(injectIntl((ContextMenu)));

