import { uniqueId } from 'lodash';
import { show_notice } from '../connected_deps';

export function get_validated_name(name:string, old_name:string, name_list:string[]):string {
  if (!name || !name.trim()) {
    // 
    show_notice({
      id: 'please_enter_name',
      type: 'error',
    });
    return old_name;
  }

  name = name.trim();
  const prev_len = name.length;

  // 
  const match_res = name.match(/[0-9a-z\s\u4e00-\u9fa5()_-]/gi);
  const processed_name = match_res && match_res.join('').trim() || '';
  if (!processed_name) {
    // 
    show_notice({
      id: 'illegal_name',
      type: 'error',
    });
    return old_name;
  }

  const processed_len = processed_name.length;
  // 
  if (prev_len !== processed_len) {
    show_notice({
      id: 'illegal_charaters_are_deleted',
      type: 'success',
    });
  }

  if (processed_name === old_name) {
    return processed_name;
  }

  const duplicated_index = name_list.indexOf(processed_name);
  if (duplicated_index >= 0) {
    return get_validated_name(auto_rename(name_list, processed_name), old_name, name_list);
  }

  return processed_name;
}

function auto_rename(name_list:string[], name:string):string {
  while (name_list.includes(name)) {
    const r = name.match(/^(.*?)\((\d+)\)$/);
    if (!r) {
      name += '(1)';
    } else {
      name = `${r[1]}(${get_oversize_number(r[2])})`;
    }
  }
  return name;
}

function get_oversize_number(name_number:string) {
  // this function is for dealing with number precision in JS when integer is larger than 2^53
  if (name_number.length > 15) {
    let start = parseInt(name_number.substring(0, 15), 10);
    const end = parseInt(name_number.substring(15, name_number.length), 10) + 1;
    let end_string = end.toString();
    if (end > Math.pow(name_number.length - 15, 10) - 1) {
      start += 1;
      end_string = end_string.substring(1, end_string.length);
    }
    return start + end_string;
  }
  return parseInt(name_number, 10) + 1;
}

/**
 * Checked if the given string is a valid number.
 *
 * @param text The string to be checked.
 */
export function is_valid_number(text:string) {
  return /^[+-]?\d*\.?\d+(e[+-]?\d+)?$/.test(text);
}

/**
 * Auto rename by increase the number in the suffix.
 *
 * e.g. "Number1" --> "Number2"
 *
 * @param prefix Prefix of the name
 * @param name_list Array of all names
 * @param range Rename range
 */
export function rename_base_on_range(prefix:string, name_list:string[], range:number) {
  // 
  const name_index_used = name_list
    .filter((name) => name.includes(prefix))
    .map((name) => parseFloat(name.split(prefix)[1]));

  // 
  const available_name_index = Array.from({ length: range }, (v, k) => k + 1).filter((i) => !name_index_used.includes(i));

  if (available_name_index.length === 0) {
    // No available name provided. Generate a random id foe name.
    return prefix + uniqueId();
  }

  // 
  return `${prefix}${available_name_index[0]}`;
}

let is_modified:boolean = false;

/**
 * 
 * @param is_m 
 */
export function set_model_modified(is_m:boolean) {
  is_modified = is_m;
}

/**
 *  ai 
 */
export function is_model_modified() {
  return is_modified;
};

