import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { StoreState, choose_step, choose_data_set } from '../../redux';

import { DataSet, TrainingStep } from '../../models/def';
import { get_init_data } from './default_data';

import './style.scss';

interface ConnectedProps extends InjectedIntlProps {
  choose_step:typeof choose_step;
  choose_data_set:typeof choose_data_set;
}

interface DataSetState {
  data_sets:DataSet[];
}

class DataSets extends React.Component<ConnectedProps, DataSetState> {
  constructor(props:ConnectedProps) {
    super(props);
    this.state = {
      data_sets: [],
    };
  }

  UNSAFE_componentWillMount() {
    const data_sets = get_init_data();
    this.setState({ data_sets });
  }

  private handle_click_data_set = (data_set:DataSet) => {
    this.props.choose_data_set(data_set);
    this.props.choose_step(TrainingStep.IMPORTING);
  }

  render() {
    return (
      <div
          styleName="main-content-wrapper"
      >
        <div
            styleName="back-to-last-step"
            onClick={this.props.choose_step.bind(this, TrainingStep.EDITING_DATA)}
            className="th1-text-0 th1-text-s2"
        >
          <div styleName="icon">
            <svg
                className="icon"
                aria-hidden="true"
            >
              <use xlinkHref="#iconicon-dropdown" />
            </svg>
          </div>
          <div styleName="text">
            {this.props.intl.formatMessage({ id: 'back' })}
          </div>
        </div>
        <div styleName="data-sets-container">
          {this.state.data_sets.map((data_set, index) => (
            <div
                styleName="data-set-wrapper"
                key={index}
                data-report-click="AI2-"
                onClick={this.handle_click_data_set.bind(this, data_set)}>
              <div
                  styleName="preview-container"
                  style={{ backgroundImage: `url(${data_set.preview})` }}
              >
                {data_set.has_label &&
                <span styleName={`preview-label ${this.props.intl.locale.startsWith('zh') ? 'zh' : ''}`}>
                  {this.props.intl.formatMessage({ id: 'data_set_label' })}
                </span>}
              </div>
              <div styleName="data-set-title">{data_set.name}</div>
              <div styleName="data-set-classification">
                {`${this.props.intl.formatMessage({ id: 'classification' })}: `}
                {data_set.classifications.join(', ')}
              </div>
            </div>))}
        </div>
      </div>
    );
  }
}

function map_state_to_props(state:StoreState) {
  return {
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      choose_step,
      choose_data_set,
    },
    dispatch);
}

export const DataSetComponent =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl((DataSets)));