import { Action } from 'redux';
import { cloneDeep } from 'lodash';
import { DataState } from '../../models/def';

export const initial_data_state:DataState = {
  data: [
    [[''], [''], [''], [''], ['']],
    [[''], [''], [''], [''], ['']],
  ],
  valid_rows: [
    [],
    [],
  ],
  should_hightlight: false,
  symbol_index: [
    [-1, -1],
    [-1, -1],
  ],
};

export interface DataAction extends Action {
  payload?:DataState;
}

// Actions
export const SET_DATA = 'ai_lab/set_data';
export function set_data(state:DataState) {
  return { type: SET_DATA, payload: state };
}

export const RESET_DATA_STATE = 'ai_lab/reset_data_state';
export function reset_data_state() {
  return { type: RESET_DATA_STATE };
}

export const CHANGE_DATA = 'ai_lab/change_data';
export function change_data(data:(number | string)[][][], symbol_index?:number[][]) {
  return { type: CHANGE_DATA, payload: { data, symbol_index } };
}

export const SHOW_HIGHTLIGHT = 'ai_lab/show_hightlight';
export function show_hightlight() {
  return { type: SHOW_HIGHTLIGHT, payload: { should_hightlight: true } };
}

export const HIDE_HIGHTLIGHT = 'ai_lab/hide_hightlight';
export function hide_hightlight() {
  return { type: HIDE_HIGHTLIGHT, payload: { should_hightlight: false } };
}

export function data_reducer(state = initial_data_state, action:any) {
  switch (action.type) {
    case SET_DATA:
      return Object.assign({}, state, action.payload);
    case CHANGE_DATA:
      const data:(string | number)[][][] = cloneDeep(action.payload.data);
      const valid_rows:number[][] = [];
      const symbol_index:number[][] = cloneDeep(action.payload.symbol_index) || [];
      data.forEach((group_data, class_index) => {
        // 
        valid_rows[class_index] = [];
        // 
        // change_datasymbol_index
        if (!action.payload.symbol_index) {
          symbol_index[class_index] = [];
          if (state.symbol_index[class_index]) {
            symbol_index[class_index] = state.symbol_index[class_index];
          } else {
            symbol_index[class_index] = [-1, -1];
          }
        }
        group_data.forEach((data_row, row_index) => {
          if (data_row.every((data_cell) => data_cell !== '')) {
            valid_rows[class_index].push(row_index);
          }
        });
      });
      return Object.assign({}, state, { data, valid_rows, symbol_index });
    case RESET_DATA_STATE:
      return cloneDeep(initial_data_state);
    case SHOW_HIGHTLIGHT:
      return Object.assign({}, state, action.payload);
    case HIDE_HIGHTLIGHT:
      return Object.assign({}, state, action.payload);
    default:
      return state;
  }
}