import { range } from 'd3-array';
import { axisBottom, axisRight } from 'd3-axis';
import { rgb } from 'd3-color';
import { select, event } from 'd3-selection';
import { scaleLinear, scaleQuantize } from 'd3-scale';

import { CLASS_COLORS, DrawData } from '../../models/def';
import './diagram.css';

export type Example2D = {
  x:number,
  y:number,
  label:number,
};

export interface HeatMapSettings {
  showAxes?:boolean;
  noSvg?:boolean;
}

/** Number of different shades (colors) when drawing a gradient heatmap */
const NUM_SHADES = 30;

/**
 * Draws a heatmap using canvas. Used for showing the learned decision
 * boundary of the classification algorithm. Can also draw data points
 * using an svg overlayed on top of the canvas heatmap.
 */
export class HeatMap {
  private settings:HeatMapSettings = {
    showAxes: true,
    noSvg: false,
  };
  private xScale:d3.ScaleLinear<number, number>;
  private yScale:d3.ScaleLinear<number, number>;
  private numSamples:number;
  private color_arr:d3.ScaleQuantize<string>[];
  private container:any;
  private canvas:d3.Selection<HTMLCanvasElement, {}, HTMLElement, any>;
  private svg:d3.Selection<SVGElement, {}, HTMLElement, any>;
  private tooltip:d3.Selection<HTMLDivElement, {}, HTMLElement, any>;

  private width:number;
  private padding:number;

  constructor(
    width:number, numSamples:number, containerSelector:string,
    userSettings?:HeatMapSettings) {
    // overwrite the defaults with the user-specified settings.
    userSettings && Object.assign(this.settings, userSettings);

    this.numSamples = numSamples;
    this.width = width;
    this.padding = this.settings.showAxes ? 20 : 0; // FIXME magic number

    // Get a range of colors.
    this.color_arr = CLASS_COLORS.map((color) => {
      const tmp_scale = scaleLinear<string>()
        .domain([0, 1])
        .range(['#ffffff', color])
        .clamp(true);
      // Due to numerical error, we need to specify
      // d3.range(0, end + small_epsilon, step)
      // in order to guarantee that we will have end/step entries with
      // the last element being equal to end.
      const color_range = range(0, 1 + 1E-9, 1 / NUM_SHADES).map((a) => tmp_scale(a));
      return scaleQuantize<string>()
        .domain([0, 1])
        .range(color_range);
    });

    const container = select(containerSelector).append('div')
      .style('width', this.width + 'px')
      .style('height', this.width + 'px')
      .style('position', 'relative');
    this.container = container;

    this.canvas = container.append('canvas')
      .attr('width', numSamples)
      .attr('height', numSamples)
      .style('width', (this.width - 2 * this.padding) + 'px')
      .style('height', (this.width - 2 * this.padding) + 'px')
      .style('position', 'absolute')
      .style('top', `${this.padding}px`)
      .style('left', `${this.padding}px`);

    if (this.settings.noSvg) { return; }
    this.svg = container.append('svg')
      .attr('width', this.width)
      .attr('height', this.width)
      .style('position', 'absolute')
      .style('top', '0')
      .style('left', '0')
      .style('overflow', 'visible') // Enable long data to be shown
      .append('g')
      .attr('transform', `translate(${this.padding},${this.padding})`);

    // Append heatmap after axes so that dots are always upper the coordinates
    this.svg.append('g').attr('class', 'train');

    this.tooltip = container.append('div')
      .attr('class', 'axis tooltip');
  }

  updateBackground(data:DrawData[][], discretize:boolean = false):void {
    const dx = data[0].length;
    const dy = data.length;

    if (dx !== this.numSamples || dy !== this.numSamples) {
      throw new Error(
        'The provided data matrix must be of size ' +
        'numSamples X numSamples');
    }

    // Compute the pixel colors; scaled by CSS.
    const context = (this.canvas.node() as HTMLCanvasElement).getContext('2d');
    if (!context) { return; }
    const image = context.createImageData(dx, dy);

    for (let y = 0, p = -1; y < dy; ++y) {
      for (let x = 0; x < dx; ++x) {
        const value = data[x][y];
        if (discretize) {
          value.density = (value.density >= 0 ? 1 : -1);
        }
        const c = rgb(this.color_arr[value.class_index](value.density));
        image.data[++p] = c.r;
        image.data[++p] = c.g;
        image.data[++p] = c.b;
        image.data[++p] = 160;
      }
    }
    context.putImageData(image, 0, 0);
  }

  resetPoints(xDomain:[number, number], yDomain:[number, number], points:Example2D[], labels:string[]) {
    this.resetRange(xDomain, yDomain, labels);
    this.svg.select('g.train').selectAll('circle').remove();
    this.updatePoints(points);
  }

  private updatePoints(points:Example2D[]):void {
    if (this.settings.noSvg) {
      throw Error('Can\'t add points since noSvg=true');
    }
    this.updateCircles(this.svg.select('g.train'), points);
  }

  private resetRange(xDomain:[number, number], yDomain:[number, number], labels:string[]) {
    // d3.scaleLinear: projection from domain to range
    this.xScale = scaleLinear()
      .domain(xDomain)
      .range([0, this.width - 2 * this.padding]);

    this.yScale = scaleLinear()
      .domain(yDomain)
      .range([this.width - 2 * this.padding, 0]);

    if (this.settings.showAxes) {
      const xAxis = axisBottom(this.xScale);
      const yAxis = axisRight(this.yScale);

      // Clear existing axes and set new ones
      this.svg.selectAll('.axis').remove();

      this.svg.append('g')
        .attr('class', 'x axis')
        .attr('transform', `translate(0,${this.width - 2 * this.padding})`)
        .call(xAxis)
        .on('mouseover', () => {
          this.tooltip.html(labels[0])
            .style('opacity', 1)
            .style('left', `${event.pageX + 1}px`)
            .style('top', `${event.pageY + 14}px`);
        })
        .on('mouseout', () => {
          this.tooltip.style('opacity', 0);
        });

      this.svg.append('g')
        .attr('class', 'y axis')
        .attr('transform', 'translate(' + (this.width - 2 * this.padding) + ',0)')
        .call(yAxis)
        .on('mouseover', () => {
          this.tooltip.html(labels[1])
            .style('opacity', 1)
            .style('left', `${event.pageX + 1}px`)
            .style('top', `${event.pageY + 14}px`);
        })
        .on('mouseout', () => {
          this.tooltip.style('opacity', 0);
        });
    }
  }

  private updateCircles(container:d3.Selection<SVGElement, {}, HTMLElement, any>, points:Example2D[]) {
    // Keep only points that are inside the bounds.
    const xDomain = this.xScale.domain();
    const yDomain = this.yScale.domain();
    points = points.filter((p) => p.x >= xDomain[0] && p.x <= xDomain[1] &&
        p.y >= yDomain[0] && p.y <= yDomain[1]);

    // Attach data to initially empty selection.
    const selection = container.selectAll('circle').data(points);

    // Insert elements to match length of points array
    // and update points to be in the correct position.
    selection.enter().append('circle')
      .attr('r', 3)
      .style('stroke', '#fff')
      .style('stroke-width', 0.7)
      .attr('cx', (d:Example2D) => this.xScale(d.x))
      .attr('cy', (d:Example2D) => this.yScale(d.y))
      .style('fill', (d:Example2D) => this.color_arr[d.label](1));

    // Remove points if the length has gone down.
    selection.exit().remove();
  }

  public destroy() {
    this.container.remove(this.svg);
    this.container.remove(this.canvas);
  }
} // Close class HeatMap.