import { uniqueId } from 'lodash';
import { TrainingStatus, TrainingState, DEFAULT_TRAINING_ITERATIONS, DEFAULT_LEARNING_RATE, SavedTrainingState } from '../../models/def';

export const initial_training_state:TrainingState = {
  is_custom_mode: false,
  training_status: 'wait_start',
  training_started_at: undefined,

  num_training_iterations: DEFAULT_TRAINING_ITERATIONS,
  learning_rate: DEFAULT_LEARNING_RATE,

  current_training_times: 0,
  current_acc: 0,
  current_loss: 0,

  data_flag: uniqueId(),
};

// Actions
export const SET_TRAINING_INFO = 'ai_lab/set_training_info';
export function set_training_info(state:SavedTrainingState) {
  return { type: SET_TRAINING_INFO, payload: state };
}

export const SET_TRAINING_STATUS = 'ai_lab/set_training_status';
export function set_training_status(status:TrainingStatus) {
  return { type: SET_TRAINING_STATUS, payload: { status } };
}

export const REFRESH_TRAINING_RESULT = 'ai_lab/refresh_training_result';
export function refresh_training_result(times:number, acc:number, loss:number) {
  return { type: REFRESH_TRAINING_RESULT, payload: { times, acc, loss } };
}

export const SET_START_TIME = 'ai_lab/set_start_time';
export function set_start_time(date:Date) {
  return { type: SET_START_TIME, payload: { date } };
}

export const SET_CUSTOM_MODE = 'ai_lab/set_custom_mode';
export function set_custom_mode(is_custom_mode:boolean) {
  return { type: SET_CUSTOM_MODE, payload: { is_custom_mode } };
}

export const SET_NUM_TRAINING_ITERATIONS = 'ai_lab/set_num_training_iterations';
export function set_num_training_iterations(num_training_iterations:number) {
  return { type: SET_NUM_TRAINING_ITERATIONS, payload: { num_training_iterations } };
}

export const SET_LEARNING_RATE = 'ai_lab/set_learning_rate';
export function set_learning_rate(learning_rate:number) {
  return { type: SET_LEARNING_RATE, payload: { learning_rate } };
}

export const RESET_DATA_FLAG = 'ai_lab/reset_data_flag';
export function reset_data_flag() {
  return { type: RESET_DATA_FLAG, payload: uniqueId() };
}

export const RESET_TRAINING_STATE = 'ai_lab/reset_training_state';
export function reset_training_state() {
  return { type: RESET_TRAINING_STATE };
}

export function training_reducer(state = initial_training_state, action:any) {
  switch (action.type) {
    case SET_TRAINING_INFO:
      return Object.assign({}, state, action.payload);
    case SET_TRAINING_STATUS:
      return Object.assign({}, state, { training_status: action.payload.status });
    case REFRESH_TRAINING_RESULT:
      return Object.assign({}, state, {
        current_training_times: action.payload.times,
        current_acc: action.payload.acc,
        current_loss: action.payload.loss,
      });
    case SET_START_TIME:
      return Object.assign({}, state, { training_started_at: action.payload.date });
    case SET_CUSTOM_MODE:
      return Object.assign({}, state, { is_custom_mode: action.payload.is_custom_mode });
    case SET_NUM_TRAINING_ITERATIONS:
      return Object.assign({}, state, { num_training_iterations: action.payload.num_training_iterations });
    case SET_LEARNING_RATE:
      return Object.assign({}, state, { learning_rate: action.payload.learning_rate });
    case RESET_TRAINING_STATE:
      return initial_training_state;
    case RESET_DATA_FLAG:
      return Object.assign({}, state, { data_flag: action.payload });
    default:
      return state;
  }
}