import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { StoreState } from '../../redux';
import { set_model_type } from '../../redux/reducers/model_reducer';
import { model_type_dict } from '../../models/def';
import './style.scss';

interface DialogProps {
  save_and_close:Function;
}

interface ConnectedProps extends InjectedIntlProps {
  choose_type:typeof set_model_type;
}

class ChooseType extends React.Component<DialogProps & ConnectedProps> {

  // TODO: add icons/details
  private get_types() {
    return model_type_dict.map((model_type, index) => (
      <div styleName="outside-wrapper" key={model_type}
          onClick={this.props.choose_type.bind(this, index)}>
        <div styleName="inside-wrapper">
          <div styleName={`${model_type}-illustration`} />
          <div styleName="title">
            {this.props.intl.formatMessage({ id: model_type })}
          </div>
          <div styleName="abstract">
            {this.props.intl.formatMessage({ id: `${model_type}_abstract` })}
          </div>
        </div>
      </div>
    ));
  }

  render() {
    return <div styleName="dialog-wrapper">
      <div styleName="dialog-header">
        <div styleName="dialog-name">AI 2.0</div>
        <div styleName="close-btn" onClick={ () => this.props.save_and_close(false) } />
      </div>
      <div styleName="dialog-content">{this.get_types()}</div>
    </div>;
  }
}

function map_state_to_props(state:StoreState, props:DialogProps) {
  return {
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      choose_type: set_model_type,
    },
    dispatch);
}

export const ChooseTypeDialog =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl(ChooseType));