import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { Provider } from 'react-redux';

import { intl, set_language } from './i18n';
import { zh_CN } from './i18n/zh_CN';
import { store_manager } from './controllers';
import { AIModelForSave, KittenAILabProps, RANGE_NUM_CLASSES } from './models/def';
import { AILabWrapper } from './main';
import {
  set_model_info,
  set_training_info,
  change_data,
  add_feature,
  add_classification,
  change_model_name,
  set_ai_lab_visibility,
  reset_data_flag,
  set_theme,
  Theme,
} from './redux';
import { set_path } from './cfg';
import { set_props_show_notice, set_props_show_confirm_dialog, set_props_report_event } from './deps';
import { ConnectedIntlProvider } from './ConnectedIntlProvider';
import { set_model_modified } from './models/utils';

/**
 * Main class used in kitten
 */
export class KittenAILab {
  private _element:HTMLElement;

  public set_model_info(model_info?:AIModelForSave) {
    //  Ai Lab 
    store_manager.get_store().dispatch(set_ai_lab_visibility(true));

    let model_modified_flag = false;
    if (model_info) {
      store_manager.get_store().dispatch(set_model_info(model_info.model_state));
      store_manager.get_store().dispatch(set_training_info(model_info.training_state));

      const symbol_index = model_info.data_state.symbol_index;
      store_manager.get_store().dispatch(change_data(model_info.data_state.data, symbol_index));

      //  data_flag 
      const has_extra_data = (idx_arr:number[]) => idx_arr.some((idx) => idx !== -1);
      if (symbol_index.some((idx_arr) => has_extra_data(idx_arr))) {
        store_manager.get_store().dispatch(reset_data_flag());
        model_modified_flag = true;
      }
    } else {
      store_manager.get_store().dispatch(add_feature());
      store_manager.get_store().dispatch(add_classification(RANGE_NUM_CLASSES[0]));
      const default_name = intl().formatMessage({ id: 'untitled_model' });
      store_manager.get_store().dispatch(change_model_name(default_name));
      model_modified_flag = true;
    }

    setTimeout(
      set_model_modified.bind(this, model_modified_flag),
      300,
    );
  }

  public set_language = set_language;

  public set_theme(theme:Theme) {
    store_manager.get_store().dispatch(set_theme(theme));
  }

  constructor(props:KittenAILabProps) {
    const {
      element,
      cdn_path,
      show_notice,
      show_confirm_dialog,
      report_event,
      language_data,
    } = props;

    this._element = element;
    const save_and_close = props.save_and_close;
    const store = store_manager.get_store();

    cdn_path && set_path(cdn_path);
    show_notice && set_props_show_notice(show_notice);
    show_confirm_dialog && set_props_show_confirm_dialog(show_confirm_dialog);
    report_event && set_props_report_event(report_event);

    set_language(language_data);

    ReactDOM.render(
      <Provider store={store} >
        <ConnectedIntlProvider>
          <AILabWrapper save_and_close={save_and_close} />
        </ConnectedIntlProvider>
      </Provider>,
      this._element,
    );
  }
}

export {
  LastStepBeforeSaving,
} from './models/def';

export type StrictMsgType = typeof zh_CN;
