import { combineReducers } from 'redux';
import { ModelState, TrainingState, DataState } from '../models/def';
import { data_reducer } from './reducers/data_reducer';
import { model_reducer } from './reducers/model_reducer';
import { training_reducer } from './reducers/training_reducer';
import { MainState, main_reducer } from './reducers/main_reducer';
import { ConfirmDialogState, confirm_dialog_reducer } from './reducers/confirm_reducer';
import { NoticeState, notice_reducer } from './reducers/notice_reducer';
import { context_menu_reducer, ContextMenuState } from './reducers/contextmenu_reducer';

export * from './reducers';
export * from './sagas';

export interface StoreState {
  model_state:ModelState;
  data_state:DataState;
  training_state:TrainingState;
  main_state:MainState;
  confirm_dialog_state:ConfirmDialogState;
  notice_state:NoticeState;
  context_menu_state:ContextMenuState;
}
const app_reducer = combineReducers<StoreState>({
  model_state: model_reducer,
  data_state: data_reducer,
  training_state: training_reducer,
  main_state: main_reducer,
  confirm_dialog_state: confirm_dialog_reducer,
  notice_state: notice_reducer,
  context_menu_state: context_menu_reducer,
});

export const RESET_STORE = 'ai_lab/reset_store';
export function reset_store() {
  return { type: RESET_STORE };
}

export const root_reducer = (state:any, action:any) => {
  if (action.type === RESET_STORE) {
    state = undefined;
  }

  return app_reducer(state, action);
};

