import * as React from 'react';
import { injectIntl, InjectedIntlProps } from 'react-intl';
import { bindActionCreators, Dispatch } from 'redux';
import { connect } from 'react-redux';

import {
  StoreState,
  choose_step,
  choose_data_set,
  show_hightlight,
  hide_hightlight,
} from '../../redux';
import { TrainingStep, MIN_DATA_LENGTH, LastStepBeforeSaving } from '../../models/def';
import { ModelNameComponent } from '../model_name';
import { show_notice, report_event } from '../../connected_deps';
import { CmTableComponent } from './table';

import './style.scss';

interface OwnProps {
  save_and_close:(save_data:boolean, last_step?:LastStepBeforeSaving) => void;
}

const acceptable_file_types = [
  '.csv',
  'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
  'application/vnd.ms-excel',
  '.xlsx',
  '.xls',
];

type Props = InjectedIntlProps & OwnProps & ReturnType<typeof map_dispatch_to_props> & ReturnType<typeof map_state_to_props>;

interface State {
  should_show_hint:boolean;
}

class EditData extends React.PureComponent<
  Props,
  State
> {
  /**
   * Reset training parameters when click go to training button.
   * If no training has been started at this time of use, refresh the heatmap immediately while changing training parameters.
   * Otherwise refresh when start new train.
   */
  constructor(props:Props) {
    super(props);

    this.state = {
      should_show_hint: false,
    };
  }

  private file_el:HTMLInputElement|null;

  private go_to_training = (is_data_valid:boolean) => {
    if (is_data_valid) {
      report_event('AI2-');
      this.props.hide_hightlight();
      this.props.choose_step(TrainingStep.TRAINING);
      return;
    }

    this.props.show_hightlight();
  }

  private render_add_dataset = () => (
    <div
        styleName="import-option"
        className="th1-text-0 th1-text-s2"
        data-report-click="AI2-"
        onClick={this.props.choose_step.bind(this, TrainingStep.CHOOSING_DATA_SET)}
    >
      {this.props.intl.formatMessage({ id: 'add_data_set' })}
    </div>
  )

  private handle_file_change = (e:React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files && e.target.files[0];
    if (!file) { return; }

    const file_extension = file.name.substring(file.name.lastIndexOf('.'));
    if (acceptable_file_types.some(
      (type) => (
        file.type === '' ?
          type.includes(file_extension) :
          Boolean(file.type.match(type))
      ),
    )) {
      this.props.choose_data_set(file);
      this.props.choose_step(TrainingStep.IMPORTING);
      return;
    }

    show_notice({
      id: 'upload_error',
      type: 'error',
    });
  }

  private render_import_data = () => (
    <div
        styleName="import-option"
        className="th1-text-0 th1-text-s2"
        onClick={() => {
          this.file_el && this.file_el.click();
        }}
        data-report-click="AI2-"
    >
      <input
          type="file"
          styleName="upload-input"
          accept={acceptable_file_types.join(', ')}
          onChange={this.handle_file_change}
          ref={(ref) => {
            this.file_el = ref;
          }}
      />
      {this.props.intl.formatMessage({ id: 'import_data' })}
    </div>
  )

  private render_footer = () => {
    const is_data_valid = this.props.valid_rows.every((valid_row) => valid_row.length >= MIN_DATA_LENGTH);

    return (
      <div styleName="content-footer">
        <div styleName="tips">
          {this.props.intl.formatMessage({ id: 'edit_data_tips' })}
        </div>
        <div styleName="btn-group">
          <ModelNameComponent />
          <div
              styleName="btn-normal left"
              className="th1-text-0 th1-text-s2 th1-border-impt-1 th1-text-hover-10 th1-bg-hover-0 th1-border-hover-0 th1-bg-active-m2 th1-border-active-m2 th1-text-active-10"
              data-report-click="AI2-"
              onClick={() => { this.props.save_and_close(true, LastStepBeforeSaving.SaveInTheDataEditPage); }}
          >
            {this.props.intl.formatMessage({ id: 'ai_lab/save_and_exit' })}
          </div>
          <div
              styleName={`training_btn ${is_data_valid ? 'btn-positive' : 'btn-negative'}`}
              className={is_data_valid ? 'th1-bg-0 th1-bg-hover-m1 th1-bg-active-m2 th1-text-10' : ''}
              onClick={this.go_to_training.bind(this, is_data_valid)}
              onMouseEnter={() => {
                if (is_data_valid) {
                  return;
                }

                this.setState({
                  should_show_hint: true,
                });
              }}
              onMouseLeave={() => {
                if (is_data_valid) {
                  return;
                }

                this.setState({
                  should_show_hint: false,
                });
              }}
          >
            {this.props.intl.formatMessage({ id: 'train' })}
            {
              !is_data_valid &&
              <div
                  styleName="disable_hint"
              >
                <svg
                    className="icon"
                    aria-hidden="true"
                >
                  <use xlinkHref="#iconicon_jinggao" />
                </svg>
              </div>
            }
            {
              this.state.should_show_hint &&
              <div styleName="hint_label">
                {this.props.intl.formatMessage({ id: 'data_insufficient_notice' })}
              </div>
            }
          </div>
        </div>
      </div>
    );
  }

  render() {
    return (
      <div styleName="editing-wrapper">
        <div styleName="import-data-wrapper">
          {this.render_add_dataset()}
          {this.render_import_data()}
        </div>
        <CmTableComponent />
        {this.render_footer()}
      </div>
    );
  }
}

function map_state_to_props(state:StoreState, own_props:OwnProps) {
  return {
    model_name: state.model_state.model_name,
    valid_rows: state.data_state.valid_rows,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      choose_step,
      choose_data_set,
      show_hightlight,
      hide_hightlight,
    },
    dispatch);
}

export const EditDataComponent =
  connect(map_state_to_props, map_dispatch_to_props)(injectIntl((EditData)));
