import { cloneDeep } from 'lodash';
import { Action } from 'redux';
import { ModelType, ModelState, RANGE_NUM_CLASSES, RANGE_NUM_FEATURES } from '../../models/def';
import { intl } from '../../i18n';
import { rename_base_on_range } from '../../models/utils';

export const initial_model_state:ModelState = {
  model_name: '',
  model_type: ModelType.DATA,
  features: [],
  classifications: [],
  hidden_layers: [4, 4],
  net: undefined,
  max: [],
  min: [],
};

export interface ModelAction extends Action {
  payload:Partial<ModelState> & { index:number } & { index:number, new_name:string } & { num:number } & { index1:number, index2:number };
}

// Actions
export const SET_MODEL_INFO = 'ai_lab/set_model_info';
export function set_model_info(model_info:ModelState) {
  return { type: SET_MODEL_INFO, payload: model_info };
}

export const CHANGE_MODEL_NAME = 'ai_lab/change_model_name';
export function change_model_name(model_name:string) {
  return { type: CHANGE_MODEL_NAME, payload: { model_name } };
}

// For data importing
export const CHANGE_CLASSIFICATION = 'ai_lab/change_classification';
export function change_classification(classifications:string[]) {
  return { type: CHANGE_CLASSIFICATION, payload: { classifications } };
}

export const CHANGE_FEATURE = 'ai_lab/change_feature';
export function change_feature(features:string[]) {
  return { type: CHANGE_FEATURE, payload: { features } };
}

// For data edit
export const ADD_CLASSIFICATION = 'ai_lab/add_classification';
export function add_classification(num:number = 1) {
  return { type: ADD_CLASSIFICATION, payload: { num } };
}

export const DEL_CLASSIFICATION = 'ai_lab/del_classification';
export function del_classification(index:number) {
  return { type: DEL_CLASSIFICATION, payload: { index } };
}

export const EDIT_CLASSIFICATION = 'ai_lab/edit_classification';
export function edit_classification(index:number, new_name:string) {
  return { type: EDIT_CLASSIFICATION, payload: { index, new_name } };
}

export const ADD_FEATURE = 'ai_lab/add_feature';
export function add_feature() {
  return { type: ADD_FEATURE };
}

export const DEL_FEATURE = 'ai_lab/del_feature';
export function del_feature(index:number) {
  return { type: DEL_FEATURE, payload: { index } };
}

export const SWAP_FEATURE = 'ai_lab/swap_feature';
export function swap_feature(index1:number, index2:number) {
  return { type: SWAP_FEATURE, payload: { index1, index2 } };
}

export const EDIT_FEATURE = 'ai_lab/edit_feature';
export function edit_feature(index:number, new_name:string) {
  return { type: EDIT_FEATURE, payload: { index, new_name } };
}

export const CHANGE_HIDDEN_LAYER = 'ai_lab/change_hidden_layer';
export function change_hidden_layer(hidden_layers:number[]) {
  return { type: CHANGE_HIDDEN_LAYER, payload: { hidden_layers } };
}

export const SET_MODEL_TYPE = 'ai_lab/set_model_type';
export function set_model_type(model_type:ModelType) {
  return { type: SET_MODEL_TYPE, payload: { model_type } };
}

export const SET_NET = 'set_net';
export function set_net(net:string) {
  return { type: SET_NET, payload: { net } };
}

export const RESET_MODEL_STATE = 'ai_lab/reset_model_state';
export function reset_model_state() {
  return { type: RESET_MODEL_STATE };
}

// Reducer
export function model_reducer(state = initial_model_state, action:ModelAction) {
  if (action.type === SET_MODEL_INFO) {
    return Object.assign({}, state, action.payload);
  } else if (action.type === CHANGE_MODEL_NAME) {
    return Object.assign({}, state, { model_name: action.payload.model_name });

  } else if (action.type === CHANGE_CLASSIFICATION) {
    return Object.assign({}, state, { classifications: action.payload.classifications });

  } else if (action.type === CHANGE_FEATURE) {
    return Object.assign({}, state, { features: action.payload.features });

  } else if (action.type === ADD_CLASSIFICATION) {
    if (state.classifications.length >= RANGE_NUM_CLASSES[1]) {
      return state;
    }
    const classifications = cloneDeep(state.classifications);
    // 
    const default_classification_name_prefix = intl().formatMessage({ id: 'classification' });

    let num = action.payload.num;
    while (num--) {
      classifications.push(rename_base_on_range(default_classification_name_prefix, classifications, RANGE_NUM_CLASSES[1]));
    }
    return Object.assign({}, state, { classifications });

  } else if (action.type === DEL_CLASSIFICATION) {
    if (state.classifications.length <= RANGE_NUM_CLASSES[0]) {
      return state;
    }
    const classifications = cloneDeep(state.classifications);
    classifications.splice(action.payload.index, 1);
    return Object.assign({}, state, { classifications });

  } else if (action.type === EDIT_CLASSIFICATION) {
    const classifications = cloneDeep(state.classifications);
    classifications[action.payload.index] = action.payload.new_name;
    return Object.assign({}, state, { classifications });

  } else if (action.type === ADD_FEATURE) {
    if (state.features.length >= RANGE_NUM_FEATURES[1]) {
      return state;
    }
    const features = cloneDeep(state.features);

    // 
    const default_feature_name_prefix = intl().formatMessage({ id: 'feature' });
    features.push(rename_base_on_range(default_feature_name_prefix, features, RANGE_NUM_FEATURES[1]));
    return Object.assign({}, state, { features });

  } else if(action.type === SWAP_FEATURE) {
    const features = cloneDeep(state.features);
    const { index1, index2 } = action.payload;

    const index2_value = features[index2];
    features[index2] = features[index1];
    features[index1] = index2_value;

    return Object.assign({}, state, { features });
  } else if (action.type === DEL_FEATURE) {
    if (state.features.length <= RANGE_NUM_FEATURES[0]) {
      return state;
    }
    const features = cloneDeep(state.features);
    features.splice(action.payload.index, 1);
    return Object.assign({}, state, { features });

  } else if (action.type === EDIT_FEATURE) {
    const features = cloneDeep(state.features);
    features[action.payload.index] = action.payload.new_name;
    return Object.assign({}, state, { features });

  } else if (action.type === CHANGE_HIDDEN_LAYER) {
    const hidden_layers = cloneDeep(action.payload.hidden_layers);
    return Object.assign({}, state, { hidden_layers });

  } else if (action.type === SET_MODEL_TYPE) {
    return Object.assign({}, state, { model_type: action.payload.model_type });

  } else if (action.type === SET_NET) {
    return Object.assign({}, state, { net: action.payload.net });

  } else if (action.type === RESET_MODEL_STATE) {
    return Object.assign({}, state, initial_model_state);

  } else {
    return state;
  }
}