import * as React from 'react';
import { connect } from 'react-redux';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { StoreState } from '../../../redux';
import './style.scss';

interface ResultProps extends InjectedIntlProps {
  current_training_times:number;
  current_acc:number;
  current_loss:number;
}

class ResultDemonstrator extends React.Component<ResultProps, {}> {
  render() {
    return <div styleName="result-demonstrator">
      <div styleName="info-title">
        {this.props.intl.formatMessage({ id: 'epoch' })}
      </div>
      <div styleName="info-content">
        <div styleName="num-training">{this.props.current_training_times}</div>
        <div styleName="training-times">
          {this.props.intl.formatMessage({ id: 'times' })}
        </div>
      </div>
      <div styleName="info-title">
        {this.props.intl.formatMessage({ id: 'accuracy' })}
      </div>
      <div styleName="info-content">
        <div styleName="accuracy">{(this.props.current_acc * 100).toFixed(2)}</div>
        <div styleName="acc-percent">%</div>
      </div>
      <div styleName="info-title">
        {this.props.intl.formatMessage({ id: 'loss' })}
      </div>
      <div styleName="info-content">
        <div styleName="loss">{(this.props.current_loss * 100).toFixed(2)}</div>
        <div styleName="loss-percent">%</div>
      </div>
    </div>;
  }
}

function map_state_to_props(state:StoreState) {
  return {
    current_training_times: state.training_state.current_training_times,
    current_acc: state.training_state.current_acc,
    current_loss: state.training_state.current_loss,
  };
}

export const ResultDemonstratorComponent =
  connect(
    map_state_to_props,
  )(injectIntl((ResultDemonstrator)));
