import {
  TrainMessage, ITER_PER_ROUND, TARGET_ACCURACY, TARGET_LOSS, STABLE_ROUND,
  STABLE_STANDARD, STABLE_BASE, TrainResult, DrawData, TARGET_RANGE,
} from '../../models/def';
import { Net } from '../../types';

const Worker = require('worker-loader!./worker.ts') as any;
const convnetjs = require<any>('convnetjs');

class MachineLearning {
  private formatted_data:number[][][];
  private dimension:number; // number of features
  private hidden_layers:number[];
  private net:Net = new convnetjs.Net();
  private worker:Worker;

  // Boundaries of data, indexed by features
  private max:number[];
  private min:number[];

  // Custom Training parameters
  private is_custom_mode:boolean;
  private num_training_iterations:number;
  private learning_rate:number;

  /**
   * Indicates whether the last time of train is manually stopped or not.
   * *Manually Stop* contains 3 situations:
   * 1. Stopped by user in auto mode by clicking stop btn.
   * 2. Stops when user change data.
   * 3. Stops when user change neuron network structure.
   */
  private manually_stop:boolean;
  /**
   * The training after manually stop should reset the net and all other demonstrated data.
   */
  private force_reset:boolean;

  // Counters, for callback every round & at the end of a train
  private history_accuracy:number[] = [];
  private history_loss:number[] = [];
  /**
   * Note the total training iterations of current model, reset when the model is reset.
   * For demonstration
   */
  private total_training_iters:number = 0;
  /**
   * Note the training iterations of since the latest start action, reset when start btn is clicked
   * For controlling training process
   */
  private training_counter:number = 0;
  private end_callback:() => void;
  private round_callback:(current_training_times:number, current_acc:number, current_loss:number, draw_data:DrawData[][], net:string) => void;

  /** ************ Change parameters ************/
  public set_callback_function(callback:() => void) {
    this.end_callback = callback;
  }

  public set_round_callback(
    callback:(current_training_times:number, current_acc:number, current_loss:number, draw_data:DrawData[][], net:string) => void) {
    this.round_callback = callback;
  }

  public set_trainer_params(is_custom_mode:boolean, iterations?:number, learning_rate?:number) {
    this.is_custom_mode = is_custom_mode;
    this.num_training_iterations = iterations || this.num_training_iterations;
    this.learning_rate = learning_rate || this.learning_rate;
  }

  public set_training_data(new_data:(number | string)[][][], dimension:number):[number, number][] {
    this.dimension = dimension;
    this.max = new Array(this.dimension).fill(-Infinity);
    this.min = new Array(this.dimension).fill(Infinity);

    new_data.forEach((classification) => {
      classification.forEach((data_row) => {
        data_row.forEach((data_cell, feature_index) => {
          this.set_boundary(feature_index, data_cell);
        });
      });
    });
    this.justify_boundary();

    this.formatted_data =
      new_data.map((classification) =>
        classification.map((data_row) =>
          data_row.map(this.justify_range)));

    // used in diagram
    return [[this.min[0], this.max[0]], [this.min[1], this.max[1]]];
  }

  private set_boundary(index:number, value:number | string) {
    const num = Number(value);
    if (this.max && this.min) {
      this.max[index] = Math.max(this.max[index], num);
      this.min[index] = Math.min(this.min[index], num);
    }
  }

  // For drawing.
  // In the preview diagram, a 10% padding should be kept (so that the input points will not be put in the axes)
  private justify_boundary() {
    if (!(this.min && this.max)) { return; }
    for (let i = 0; i < this.min.length; i++) {
      const offset = (this.max[i] - this.min[i]) * 0.1;
      this.max[i] = Math.ceil(this.max[i] + offset);
      this.min[i] = Math.floor(this.min[i] - offset);
    }
  }

  public set_hidden_layers(hidden_layers:number[]) {
    this.hidden_layers = hidden_layers;
  }

  public set_net(json_net:string) {
    this.net.fromJSON(JSON.parse(json_net));
  }

  /** ************ Training ************/

  public preview() {
    if (!this.worker) {
      this.init_worker();
    }

    const message:TrainMessage = {
      type: 'preview',
      data: this.formatted_data,
      dimension: this.dimension,
      hidden_layers: this.hidden_layers,
    };
    this.worker.postMessage(JSON.stringify(message));
  }

  public start(should_reset:boolean = true) {
    if (!this.worker) {
      this.init_worker();
    }

    should_reset = should_reset || this.force_reset;
    this.training_counter = 0;
    this.manually_stop = false;

    if (should_reset) {
      this.total_training_iters = 0;
      this.history_accuracy = [];
      this.history_loss = [];
      this.force_reset = false;
    }

    const message:TrainMessage = {
      type: 'start',
      should_reset,

      data: should_reset ? this.formatted_data : undefined,
      dimension: should_reset ? this.dimension : undefined,
      hidden_layers: should_reset ? this.hidden_layers : undefined,

      is_custom_mode: this.is_custom_mode,
      training_iterations: ITER_PER_ROUND,
      learning_rate: this.learning_rate,
    };

    this.worker.postMessage(JSON.stringify(message));
  }

  public reset_data() {
    if (!this.worker) {
      this.init_worker();
    }

    this.total_training_iters = 0;
    this.history_accuracy = [];
    this.history_loss = [];

    const message:TrainMessage = {
      type: 'preview',
      data: this.formatted_data,
      dimension: this.dimension,
      hidden_layers: this.hidden_layers,
    };
    this.worker.postMessage(JSON.stringify(message));
  }

  // Project the input values into range [-5, 5] (the target range)
  private justify_range = (value:number | string, feature_index:number) => {
    const min = this.min[feature_index];
    const max = this.max[feature_index];

    //  0 0 
    if (min === max) {
      return 0;
    }

    const target_min = TARGET_RANGE[0];
    const target_max = TARGET_RANGE[1];

    return (Number(value) - min) / (max - min) *
    (target_max - target_min) + target_min;
  }

  private init_worker() {
    this.worker = new Worker();
    this.worker.onmessage = this.worker_listener;
  }

  private worker_listener = (message:MessageEvent) => {
    const result:TrainResult = message.data;

    this.round_callback(
      result.type === 'preview' ? 0 : this.total_training_iters,
      result.type === 'preview' ? 0 : result.accuracy,
      result.type === 'preview' ? 0 : result.loss,
      result.draw_data,
      result.net,
    );

    if (result.type === 'preview') {
      return;
    }
    this.history_accuracy.push(result.accuracy);
    this.history_loss.push(result.loss);

    const should_continue = this.judge_train_process();

    if (!should_continue) {
      this.net.fromJSON(JSON.parse(result.net));
      this.end_callback && this.end_callback();
      return;
    }

    // Start next train round when necessary
    const train_message:TrainMessage = {
      type: 'continue',
      should_reset: false,
    };
    this.total_training_iters += ITER_PER_ROUND;
    this.training_counter += ITER_PER_ROUND;
    this.worker.postMessage(JSON.stringify(train_message));
  }

  /**
   * Decide whether the train process should continue or not.
   * @returns {Boolean} true if should continue, false if should end
   */
  private judge_train_process() {
    if (this.manually_stop) {
      this.manually_stop = false;
      __DEV__ && console.info('Manually force stop');
      return false;
    }
    if (this.is_custom_mode) {
      if (this.training_counter < this.num_training_iterations) {
        return true;
      }
      __DEV__ && console.info('Reach customized training iterations');
      return false;
    }

    const current_training_rounds = this.history_accuracy.length - 1;
    if (this.history_accuracy[current_training_rounds] >= TARGET_ACCURACY &&
      this.history_loss[current_training_rounds] <= TARGET_LOSS) {
      __DEV__ && console.info('Reach acceptale targets');
      return false;
    }
    const current_statble_round = current_training_rounds - STABLE_ROUND;
    if (current_training_rounds > STABLE_BASE &&
      (this.history_loss[current_statble_round] - this.history_loss[current_training_rounds] < STABLE_STANDARD) &&
      this.history_accuracy[current_training_rounds] - this.history_accuracy[current_statble_round] < STABLE_STANDARD) {
      __DEV__ && console.info('Not perfect but stable');
      return false;
    }
    return true;
  }

  /**
   * Manually stop current training.
   * And force the next train process to reset net.
   */
  public stop(force_reset:boolean = true) {
    // the parameter 'manually_stop' affects judge_train_process
    // if this function is called, the training process will stop when the current round ends
    this.manually_stop = true;
    this.force_reset = force_reset;
  }

  /** ************ Predict ************/
  public predict(data:number[]):number[] {
    const formatted_data =
      new convnetjs.Vol(data.map(this.justify_range));
    const result = this.net.forward(formatted_data);
    return Array.prototype.map.call(result.w, (weight:number) => weight);
  }

  public generate_net(feature_number:number, classification_number:number) {
    const layers:any[] = [];
    layers.push({ type: 'input', out_sx: 1, out_sy: 1, out_depth: feature_number });
    [4, 4].forEach((num_neurons) => {
      layers.push({ type: 'fc', num_neurons, activation: 'tanh' }); // TODO: choose proper activation function
    });
    layers.push({ type: 'softmax', num_classes: classification_number });
    const net = new convnetjs.Net();
    net.makeLayers(layers);
    return JSON.stringify(net.toJSON());
  }

  public get_data_boundary(new_data:(number | string)[][][], dimension:number) {
    const max = new Array(dimension).fill(-Infinity);
    const min = new Array(dimension).fill(Infinity);
    new_data.forEach((classification) => {
      classification.forEach((data_row) => {
        data_row.forEach((data_cell, feature_index) => {
          const num = Number(data_cell);
          if (max && min) {
            max[feature_index] = Math.max(max[feature_index], num);
            min[feature_index] = Math.min(min[feature_index], num);
          }
        });
      });
    });
    for (let i = 0; i < dimension; i++) {
      const offset = (max[i] - min[i]) * 0.1;
      max[i] = Math.ceil(max[i] + offset);
      min[i] = Math.floor(min[i] - offset);
    }
    return {
      max,
      min,
    };
  }
}

export const machine_learning = new MachineLearning();
