import * as React from 'react';
import { cloneDeep } from 'lodash';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { InjectedIntlProps, injectIntl } from 'react-intl';

import {
  StoreState, set_training_status, set_feature_noticed,
  change_data,
  add_classification, edit_classification, del_classification,
  add_feature, edit_feature, del_feature, reset_data_flag, swap_feature,
  hide_hightlight,
} from '../../../redux';
import { DEFAULT_DATA_LENGTH, RANGE_NUM_CLASSES, RANGE_NUM_FEATURES, MIN_DATA_LENGTH } from '../../../models/def';
import { get_validated_name, is_valid_number } from '../../../models/utils';
import { machine_learning } from '../../../controllers';
import { show_context_menu } from '../../../redux/reducers/contextmenu_reducer';
import { show_notice, show_confirm_dialog, report_event } from '../../../connected_deps';

import './style.scss';

interface TableProps extends InjectedIntlProps {
  features:string[];
  classifications:string[];
  data:(number | string)[][][];
  valid_rows:number[][];
  symbol_index:number[][];
  should_hightlight:boolean;

  change_data:typeof change_data;

  add_classification:typeof add_classification;
  edit_classification:typeof edit_classification;
  del_classification:typeof del_classification;

  add_feature:typeof add_feature;
  edit_feature:typeof edit_feature;
  del_feature:typeof del_feature;

  set_training_status:typeof set_training_status;

  should_notice_feature_add:boolean;
  set_feature_noticed:typeof set_feature_noticed;
  reset_data_flag:typeof reset_data_flag;
  show_context_menu:typeof show_context_menu;
  swap_feature:typeof swap_feature;
  hide_hightlight:typeof hide_hightlight;
}

// TODO avioid magic numbers

interface TableState {
  current_tab:number;
  current_row?:number;
  current_column?:number;
  tab_index_to_show_input?:number;
  column_index_to_show_input?:number;
  is_editing_data?:boolean;
}

class CmTable extends React.PureComponent<TableProps, TableState> {
  /**
   * Note the value of data cell when focus it.
   * Stop the training when the value changes.
   */
  private last_value:string | number;

  /**
   * Note the old name when changing classification or feature names.
   * Used for checking duplication.
   */
  private last_name:string;
  private name_list:string[];
  private tab_prev_selected:boolean;
  private tab_click_count:number;
  private column_prev_selected:boolean;
  private column_click_count:number;

  constructor(props:TableProps) {
    super(props);
    this.state = {
      current_tab: 0,
    };
  }

  private table_el:HTMLDivElement|null;

  getSnapshotBeforeUpdate(prevProps:TableProps, prevState:TableState) {
    if (!this.table_el) {
      return null;
    }

    return this.table_el.scrollHeight;
  }

  componentDidUpdate(prev_props:TableProps, prev_state:TableState, snapshot:number) {
    if (!snapshot) {
      return;
    }

    if (!this.table_el) {
      return;
    }

    if (this.table_el.scrollHeight > snapshot) {
      this.table_el.scrollTop = this.table_el.scrollHeight - this.table_el.clientHeight;
    }
  }

  static getDerivedStateFromProps(next_props:TableProps, next_state:TableState):TableState|null {
    if (!next_props.should_hightlight) {
      return null;
    }

    const class_index = next_props.valid_rows.findIndex((valid_row) => valid_row.length < MIN_DATA_LENGTH);
    if (class_index === -1) {
      return null;
    }

    if (!next_state.is_editing_data) {
      return {
        current_tab: class_index,
      };
    }

    return null;
  }


  private get_intl = (id:string) => this.props.intl.formatMessage({ id });

  private stop_training_and_force_reset = () => {
    machine_learning.stop();
    this.props.set_training_status('wait_start');
    this.props.reset_data_flag();
  }

  private handle_row_del = (index_data_row:number) => {
    this.props.hide_hightlight();
    const data = cloneDeep(this.props.data);
    if (data[this.state.current_tab].length <= MIN_DATA_LENGTH) { return; }
    this.stop_training_and_force_reset();

    data[this.state.current_tab].splice(index_data_row, 1);
    this.props.change_data(data);
    this.setState({ current_row: undefined });
  }

  private handle_row_add = () => {
    this.props.hide_hightlight();
    this.stop_training_and_force_reset();

    const data = cloneDeep(this.props.data);
    const empty_row = new Array(this.props.features.length).fill('');
    data[this.state.current_tab].push(empty_row);
    this.props.change_data(data);
  }

  private handle_data_edit = (row_index:number, feature_index:number, new_data:number | string) => {
    const data = cloneDeep(this.props.data);
    data[this.state.current_tab][row_index][feature_index] = new_data;
    this.props.change_data(data);
  }

  private is_classification_reached_maxium = () => this.props.classifications.length >= RANGE_NUM_CLASSES[1];

  private handle_classification_add = () => {
    if (this.is_classification_reached_maxium()) {
      show_notice({
        id: 'ai_lab/at_most_4_classificaitons',
      });
      return;
    }

    this.props.hide_hightlight();
    this.stop_training_and_force_reset();
    const new_tab = this.props.classifications.length;
    const data = cloneDeep(this.props.data);

    let i = DEFAULT_DATA_LENGTH;
    const training_data = [];
    while (i-- > 0) {
      training_data.push(new Array(this.props.features.length).fill(''));
    }
    data.push(training_data);

    this.props.change_data(data);
    this.props.add_classification();

    // Navigate to the newestclassification
    this.setState({ current_tab: new_tab });
  }

  private handle_classsification_del = (class_index:number, e:Event) => {
    e.stopPropagation();
    if (this.props.classifications.length <= RANGE_NUM_CLASSES[0]) { return; }

    const callback = () => {
      this.props.hide_hightlight();
      this.stop_training_and_force_reset();
      const data = cloneDeep(this.props.data);
      data.splice(class_index, 1);

      const update_data = () => {
        this.props.del_classification(class_index);
        this.props.change_data(data);
      };
      let { current_tab } = this.state;
      if (current_tab >= class_index) {
        //  tab index  tab index  tab index - 1
        current_tab = current_tab > class_index ? current_tab - 1 : 0;
        this.setState(
          { current_tab },
          update_data,
        );
        return;
      }
      update_data();
    };

    show_confirm_dialog(
      callback,
      this.props.intl.formatMessage({ id: 'ai_lab/delete_classification' }, { classification: this.props.classifications[class_index] }),
      this.props.intl.formatMessage({ id: 'ai_lab/delete_classification_content' }), { text: 'delete', type: 'danger' }, 'cancel');
  }

  // TODO error handling
  private handle_classification_name_edit = (class_index:number, new_name:string) => {
    this.props.edit_classification(class_index, new_name);
  }

  private handle_classification_name_validation = (class_index:number, new_name:string) => {
    const validated_name = get_validated_name(new_name, this.last_name, this.name_list);
    this.handle_classification_name_edit(class_index, validated_name);
  }

  private is_feature_reached_maxium = () => this.props.features.length >= RANGE_NUM_FEATURES[1];

  // TODO add notice & maybe add '' to end of data?
  private handle_feature_add = () => {
    if (this.is_feature_reached_maxium()) {
      show_notice({
        id: 'ai_lab/at_most_4_features',
      });
      return;
    }

    this.props.hide_hightlight();
    this.props.add_feature();

    // 
    const data = cloneDeep(this.props.data);
    const data_c = data
      .map((classification) =>
        classification.map((data_row) =>
          data_row.concat(new Array(1).fill(''))));
    this.props.change_data(data_c);

    this.stop_training_and_force_reset();
    if (this.props.should_notice_feature_add) {
      show_notice({
        id: 'new_feature_notice',
      });
      this.props.set_feature_noticed();
    }
  }

  private handle_feature_name_edit = (feature_index:number, new_name:string) => {
    this.props.edit_feature(feature_index, new_name);
  }

  private handle_feature_name_validation = (feature_index:number, new_name:string) => {
    const validated_name = get_validated_name(new_name, this.last_name, this.name_list);
    this.handle_feature_name_edit(feature_index, validated_name);
  }

  private handle_feature_del = (feature_index:number) => {
    if (this.props.features.length <= RANGE_NUM_FEATURES[0]) {
      return;
    }
    const callback = () => {
      this.props.hide_hightlight();
      const data = cloneDeep(this.props.data).map((classification) => classification.map((data_row) => {
        data_row.splice(feature_index, 1);
        return data_row;
      }));
      this.props.del_feature(feature_index);
      this.props.change_data(data);
      this.stop_training_and_force_reset();
    };

    show_confirm_dialog(
      callback,
      this.props.intl.formatMessage({ id: 'ai_lab/delete_feature' }, { feature: this.props.features[feature_index] }),
      this.props.intl.formatMessage({ id: 'ai_lab/delete_feature_content' }),
      { text: 'delete', type: 'danger' }, 'cancel');
  }

  private handle_feature_swap = (idx1:number, idx2:number) => {
    const data = cloneDeep(this.props.data).map((classification) => classification.map((data_row) => {
      const idx2_value = data_row[idx2];
      data_row[idx2] = data_row[idx1];
      data_row[idx1] = idx2_value;
      return data_row;
    }));
    this.props.swap_feature(idx1, idx2);
    this.props.change_data(data);
    this.stop_training_and_force_reset();
  }

  private handle_tab_mouse_up = (index:number, e:React.MouseEvent<HTMLDivElement>) => {
    if(e.button === 2){
      return;
    }
    this.tab_click_count += 1;
    setTimeout(() => {
      if (this.tab_click_count === 1) {
        if (this.tab_prev_selected && this.state.current_tab === index) {
          this.setState({ tab_index_to_show_input: index });
        }
      }
      this.tab_click_count = 0;
    }, 300);
  }

  private get_class_tabs = () => {
    const tabs:any[] = [];
    const can_delete_classification = this.props.classifications.length > RANGE_NUM_CLASSES[0];
    this.props.classifications.forEach((class_name, index) => {
      tabs.push(
        <div
            key={index}
            styleName={index === this.state.current_tab ? 'tab-chosen' : 'tab'}
            className={`${index === this.state.current_tab ? 'th1-bg-0 th1-text-10 th1-text-s1 ' : 'th1-bg-6 th1-bg-hover-4 th1-bg-active-3 th1-bg-selected-3 th1-text-0 th1-text-s1 '}`}
            onClick={() => {
              this.tab_prev_selected = this.state.current_tab === index ? true : false;
              this.props.hide_hightlight();
              this.setState({ current_tab: index });
            }}
            onMouseUp={this.handle_tab_mouse_up.bind(this, index)}
            onContextMenu={ (e) => {
              e.preventDefault();

              this.setState({
                current_tab: index,
              });

              const { pageX, pageY } = e;
              this.props.show_context_menu({
                x: pageX,
                y: pageY,
                menu_items: [
                  {
                    text: 'ai_lab/rename',
                    cb: () => {
                      this.setState({
                        tab_index_to_show_input: index,
                      });
                    },
                  },
                ],
              });
            }}
        >
          <div styleName="classification-name">
            {class_name}
            {
              this.state.tab_index_to_show_input === index &&
              <input
                  type="text"
                  styleName="classification-name input-editing"
                  value={class_name}
                  className={`${index === this.state.current_tab ? 'th1-bg-0' : 'th1-bg-6'} th1-selected-6 ${index === this.state.current_tab ? 'th1-text-10 th1-text-s1 ' : 'th1-text-0 th1-text-s1 '}`}
                  autoFocus
                  maxLength={10}
                  onChange={(e) => { this.handle_classification_name_edit(index, e.target.value); }}
                  onFocus={(e) => {
                    e.target.select();
                    this.last_name = class_name;
                    this.name_list = cloneDeep(this.props.classifications);
                  }}
                  onBlur={(e) => {
                    this.handle_classification_name_validation(index, e.target.value);
                    this.setState({ tab_index_to_show_input: undefined });
                  }} />
            }
          </div>
          {
            can_delete_classification &&
            <div
                styleName="btn-del-class"
                data-report-click="AI2-"
                onClick={this.handle_classsification_del.bind(this, index)}
            >
              <svg
                  className="icon"
                  aria-hidden="true"
              >
                <use xlinkHref="#iconicon-delete" />
              </svg>
            </div>
          }
        </div>);
    });

    tabs.push(
      <div
          key="add"
          styleName="add_btn_container"
      >
        <div
            styleName="add_classification"
            onClick={this.handle_classification_add}
            data-report-click="AI2-"
            className={
              this.is_classification_reached_maxium() ?
                'th1-text-t3' :
                'th1-text-0 th1-text-s2'
            }
        >
          <div styleName="icon">
            <svg
                className="icon"
                aria-hidden="true"
            >
              <use xlinkHref="#iconicon_add" />
            </svg>
          </div>
          <div styleName="text">{this.get_intl('classification')}</div>
        </div>
        <div
            styleName="split"
            className="th1-bg-6"
        />
        <div
            styleName="add_feature"
            onClick={this.handle_feature_add}
            data-report-click="AI2-"
            className={
              this.is_feature_reached_maxium() ?
                'th1-text-t3' :
                'th1-text-0 th1-text-s2'
            }
        >
          <div styleName="icon">
            <svg
                className="icon"
                aria-hidden="true"
            >
              <use xlinkHref="#iconicon_add" />
            </svg>
          </div>
          <div styleName="text">{this.get_intl('feature')}</div>
        </div>
      </div>,
    );

    return (
      <div
          styleName="tabs-container"
          className="th1-bg-9"
      >
        {tabs}
      </div>
    );
  }

  private handle_column_mouse_up = (index:number, e:React.MouseEvent<HTMLDivElement>) => {
    if(e.button === 2){
      return;
    }
    this.column_click_count += 1;
    setTimeout(() => {
      if (this.column_click_count === 1) {
        if (this.column_prev_selected && this.state.current_column === index) {
          this.setState({ column_index_to_show_input: index });
        }
      }
      this.column_click_count = 0;
    }, 300);
  }

  private get_table_header = () => {
    const can_delete_feature = this.props.features.length > RANGE_NUM_FEATURES[0];
    return (
      <thead
          styleName="table-head"
          className="th1-bg-0"
      >
        <tr styleName={this.state.current_row === 0 ? 'header-r' : 'header-r'}>
          <th
              styleName="index-column"
              className="th1-text-10 th1-text-s1"
          >
            #
          </th>
          {this.props.features.map((feature, index) => {
            let column_style = '';
            if (this.state.current_column !== undefined) {
              if (index === this.state.current_column) {
                column_style = 'chosen-column-header';
              }
              if (index === (this.state.current_column + 1)) {
                column_style = 'after-chosen-column';
              }
            }
            return (
              <th
                  key={index}
                  styleName={`table-header ${column_style}`}
                  tabIndex={-1}
                  onClick={(e) => {
                    this.column_prev_selected = this.state.current_column === index ? true : false;
                    if (e.target instanceof HTMLInputElement) { return; }
                    this.setState({ current_row: undefined, current_column: index });
                  }}
                  onBlur={() => { this.setState({ current_column: undefined }); }}
                  onMouseUp={this.handle_column_mouse_up.bind(this, index)}
                  onContextMenu={(e) => {
                    e.preventDefault();

                    this.setState({ current_row: undefined, current_column: index });

                    const { pageX, pageY } = e;
                    this.props.show_context_menu({
                      x: pageX,
                      y: pageY,
                      menu_items: [
                        {
                          text: 'ai_lab/move_to_left',
                          cb: () => {
                            this.handle_feature_swap(index, index - 1);
                          },
                          disabled: index === 0,
                        },
                        {
                          text: 'ai_lab/move_to_right',
                          cb: () => {
                            this.handle_feature_swap(index, index + 1);
                          },
                          disabled: index === this.props.features.length - 1,
                        },
                        {
                          text: 'ai_lab/rename',
                          cb: () => {
                            this.setState({
                              column_index_to_show_input: index,
                            });
                          },
                        },
                      ],
                    });
                  }}
              >
                <div styleName="feature_name_container">
                  <div
                      styleName="feature-name"
                      className="th1-text-10 th1-text-s1 th1-bg-0"
                  >
                    {feature}
                    {
                      this.state.column_index_to_show_input === index &&
                      <input
                          type="text"
                          styleName="feature-name input-editing"
                          value={feature}
                          autoFocus
                          className={'th1-bg-0 th1-selected-6 th1-text-10 th1-text-s1'}
                          maxLength={10}
                          onChange={(e) => { this.handle_feature_name_edit(index, e.target.value); }}
                          onFocus={(e) => {
                            e.target.select();
                            this.last_name = feature;
                            this.name_list = cloneDeep(this.props.features);
                          }}
                          onBlur={(e) => {
                            report_event('AI2-');
                            this.handle_feature_name_validation(index, e.target.value);
                            this.setState({ column_index_to_show_input: undefined });
                          }}
                      />
                    }
                  </div>
                </div>
                {
                  can_delete_feature &&
                  <div
                      styleName="btn-del-feature"
                      className="th1-text-10 th1-text-s1"
                      data-report-click="AI2-"
                      onClick={this.handle_feature_del.bind(this, index)}
                  >
                    <svg
                        className="icon"
                        aria-hidden="true"
                    >
                      <use xlinkHref="#iconicon-delete" />
                    </svg>
                  </div>
                }
              </th>
            );
          })}
        </tr>
      </thead>
    );
  }

  private get_data_row = (data_row:(string | number)[], row_index:number) => {
    const row_style = row_index === this.state.current_row ? 'chosen-row' :
      this.state.current_row && row_index === this.state.current_row - 1 ? 'before-chosen-row' : '';
    const new_index = this.props.symbol_index[this.state.current_tab];
    const can_delete_data_row = this.props.data[this.state.current_tab].length > MIN_DATA_LENGTH;
    // kittennew_index[0]new_index[1]new-index-cell
    const index_cell_style = row_index >= new_index[0] && row_index <= new_index[1] ? 'new-index-cell' : 'index-cell';
    return (
      <tr
          key={row_index}
          styleName={row_style}
      >
        <td
            tabIndex={-1}
            onClick={() => { this.setState({ current_row: row_index, current_column: undefined }); }}
            onBlur={() => { this.setState({ current_row: undefined }); }}
            styleName={index_cell_style}
            className="th1-bg-7"
        >
          {row_index + 1}
          {
            can_delete_data_row &&
            <div
                styleName="btn-del-row"
                onClick={(e) => {
                  e.stopPropagation();
                  this.handle_row_del(row_index);
                }}
                data-report-click="AI2-"
            >
              <svg
                  className="icon"
                  aria-hidden="true"
              >
                <use xlinkHref="#iconicon-delete" />
              </svg>
            </div>
          }
        </td>
        {this.props.features.map((feature, index) => {
          let column_style = '';
          if (this.state.current_column !== undefined) {
            if (index === this.state.current_column) {
              column_style = 'chosen-column';
            }
            if (index === this.state.current_column + 1) {
              column_style = 'after-chosen-column';
            }
            if (index === this.state.current_column &&
              row_index === this.props.data[this.state.current_tab].length - 1) {
              column_style += '-bottom';
            }
          }

          const bg_class_name =
            row_index % 2 !== 0 ?
              index === RANGE_NUM_FEATURES[1] - 1 ? 'th1-bg-9' : 'th1-bg-9' : 'th1-bg-8';

          return (
            <td
                key={index}
                styleName={`${column_style} ${this.props.should_hightlight && data_row[index] === '' && row_index < MIN_DATA_LENGTH ? 'hightlight' : ''}`}
                className={`${bg_class_name}`}
            >
              <input
                  type="text"
                  value={typeof data_row[index] === 'string' || typeof data_row[index] === 'number' ? data_row[index] : ''}
                  onFocusCapture={(e) => {
                  // Check if user edit the column for the first time
                    if (localStorage.getItem('ai-lab-data-column-first-edit')) {
                      return;
                    }

                    localStorage.setItem('ai-lab-data-column-first-edit', 'true');
                    show_notice({
                      id: 'input_can_only_be_number',
                    });
                  }}
                  onChange={(e:React.ChangeEvent<HTMLInputElement>) => {
                  // Prevent the non-minus, non-number and non-dot chars being input
                    const new_val = e.target.value.replace(/[^\-\d.]/g, '');
                    this.handle_data_edit(row_index, index, new_val);
                  }}
                  onFocus={(e) => {
                    this.last_value = e.target.value;
                    this.setState({
                      is_editing_data: true,
                    });
                  }}
                  onBlur={(e) => {
                    this.setState({
                      is_editing_data: false,
                    });

                    let val;
                    if (is_valid_number(e.target.value)) {
                      val = String(parseFloat(e.target.value));
                    } else {
                      val = this.last_value;
                    }

                    let new_val:''|number = parseFloat(val as string);
                    new_val = isNaN(new_val) ? '' : new_val;

                    this.handle_data_edit(row_index, index, new_val);
                    this.stop_training_and_force_reset();
                  }} />
            </td>
          );
        })}
      </tr>
    );
  }

  private get_table_content = () => {
    if (this.state.current_tab > this.props.data.length - 1) {
      return [];
    }

    const content = this.props.data[this.state.current_tab].map(this.get_data_row);
    while (content.length < MIN_DATA_LENGTH) {
      const empty_row = new Array(this.props.features.length).fill('');
      content.push(this.get_data_row(empty_row, content.length));
    }
    return content;
  }

  private handle_keydown = (e:KeyboardEvent) => {
    if (e.keyCode !== 8 && e.keyCode !== 46) { // keycode of backspace and del
      return;
    }
    // Do not trigger delete event when editing text or data
    if (document.activeElement instanceof HTMLInputElement) {
      return;
    }
    if (typeof this.state.current_column === 'number') {
      this.handle_feature_del(this.state.current_column);
    } else if (typeof this.state.current_row === 'number') {
      this.handle_row_del(this.state.current_row);
    }
  }

  componentDidMount() {
    window.addEventListener('keydown', this.handle_keydown);
  }

  componentWillUnmount() {
    window.addEventListener('keydown', this.handle_keydown);
  }

  render() {
    if (this.props.data.length < 1) { return null; }
    return (
      <div
          styleName="table-container"
          onKeyUpCapture={(e) => {
            if (e.keyCode !== 13) { return; }
            const current_focus = document.activeElement as HTMLElement;
            if (!current_focus) { return; }
            current_focus.blur();
          }}
          onClickCapture={(e) => { if (e.target instanceof HTMLInputElement) { e.stopPropagation(); } }}>
        {this.get_class_tabs()}
        <div
            styleName="table-content"
            style={this.state.current_tab === 0 ? undefined : { borderRadius: '4px' }}
        >
          <div styleName="thead-wrapper">
            <div
                styleName="table_inner_wrapper"
                className="th1-bg-0"
            >
              <table styleName="table">
                {this.get_table_header()}
              </table>
            </div>
          </div>
          <div
              styleName="table-body"
              ref={(ref) => {
                this.table_el = ref;
              }}
          >
            <div
                styleName="table_inner_wrapper"
                className="th1-bg-7"
            >
              <table styleName="table">
                <tbody>{this.get_table_content()}</tbody>
              </table>
            </div>
          </div>
          <div styleName="add_row_btn_container">
            <div
                styleName="btn-add-row"
                className="th1-text-0 th1-text-s2 th1-bg-6 th1-bg-hover-4 th1-bg-active-3 th1-bg-selected-3"
                data-report-click="AI2-"
                onClick={this.handle_row_add}
            >
              <svg
                  className="icon"
                  aria-hidden="true"
              >
                <use xlinkHref="#iconicon_add" />
              </svg>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

function map_state_to_props(state:StoreState) {
  return {
    features: state.model_state.features,
    classifications: state.model_state.classifications,
    data: state.data_state.data,
    valid_rows: state.data_state.valid_rows,
    symbol_index: state.data_state.symbol_index,
    should_hightlight: state.data_state.should_hightlight,
    should_notice_feature_add: state.main_state.should_show_feature_add_notice,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      change_data,
      add_classification,
      edit_classification,
      del_classification,
      add_feature,
      edit_feature,
      del_feature,
      set_training_status,
      set_feature_noticed,
      reset_data_flag,
      show_context_menu,
      swap_feature,
      hide_hightlight,
    },
    dispatch);
}

export const CmTableComponent =
  (connect(map_state_to_props, map_dispatch_to_props)(injectIntl((CmTable))));
