import { takeLatest, fork } from 'redux-saga/effects';
import { Action } from 'redux';
import { debounce } from 'lodash';

import { set_model_modified } from '../../models/utils';

import {
  EDIT_FEATURE,
  EDIT_CLASSIFICATION,
  CHANGE_MODEL_NAME,
  SET_TRAINING_STATUS,
  SET_NET,
  CHANGE_HIDDEN_LAYER,
  SET_NUM_TRAINING_ITERATIONS,
  SET_TRAINING_INFO,
  CHANGE_DATA,
  SET_CUSTOM_MODE,
} from '../reducers';

export function* model_sagas() {
  yield fork(watch_model_modification);
}

type Map = {[type:string]:boolean};
/**
 *  action
 */
const MODEL_OPERATION = [
  EDIT_FEATURE,
  EDIT_CLASSIFICATION,
  CHANGE_DATA,
  CHANGE_MODEL_NAME,
  SET_TRAINING_STATUS,
  SET_NET,
  CHANGE_HIDDEN_LAYER,
  SET_NUM_TRAINING_ITERATIONS,
  SET_TRAINING_INFO,
  SET_CUSTOM_MODE,
];

function* watch_model_modification() {
  yield takeLatest((a:Action) => MODEL_OPERATION.includes(a.type), debounce(set_model_modified.bind(this, true), 50));
}
