
import * as React from 'react';

import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { InjectedIntlProps, injectIntl } from 'react-intl';
import { StoreState, change_hidden_layer } from '../../../redux';
import { ILayer, FullyConnLayer } from '../../../types';

import './style.scss';

const input_color = '#3DD1AE';
const transition_color = '#FF9948';
const svg_width = 415;
const in_out_dis = 70;
const top_dis = 28;
const neuron_d = 38;
const neuron_top_dis = top_dis + neuron_d;

const MAX_NUM_LAYERS = 4;
const MAX_NUM_NEURONS = 5;
const DEFAULT_NUM_NEURONS = 2;

const SINGLE_DIS_LEFT = (svg_width - neuron_d) / 2;
const arc_ratio = 0.75;
const arc_ratio_between_layers = 0.35;

interface DiagramProps extends InjectedIntlProps {
  network_data:number[];
  on_network_change:typeof change_hidden_layer;
  is_training:boolean;
  net:string;
  is_custom_mode:boolean;
}
interface DiagramState {
  weight:number[][][];
}

class NeuralNetworkDiagram extends React.PureComponent<DiagramProps, DiagramState> {
  private neurons_area_width = svg_width - 2 * in_out_dis;
  private dis_between_layers:number = (this.neurons_area_width - neuron_d) / (this.props.network_data.length - 1);

  constructor(props:DiagramProps) {
    super(props);
    this.state = {
      weight: [
        [
          [0.5, 0.5, 0.5, 0.5],
        ],
        [
          [0.5, 0.5, 0.5, 0.5],
          [0.5, 0.5, 0.5, 0.5],
          [0.5, 0.5, 0.5, 0.5],
          [0.5, 0.5, 0.5, 0.5],
        ],
        [
          [0.5],
          [0.5],
          [0.5],
          [0.5],
        ],
      ],
    };
  }

  /** ***************** Logic ********************/
  private on_add_neuron = (layer_index:number) => {
    const hidden_layers = this.props.network_data;
    if (hidden_layers[layer_index] >= MAX_NUM_NEURONS) { return; }
    hidden_layers[layer_index]++;
    this.props.on_network_change(hidden_layers);
  }

  private on_del_neuron = (layer_index:number) => {
    const hidden_layers = this.props.network_data;
    if (hidden_layers[layer_index] <= 1) { return; }
    hidden_layers[layer_index] = hidden_layers[layer_index] - 1;
    this.props.on_network_change(hidden_layers);
  }

  private on_add_layer = () => {
    const hidden_layers = this.props.network_data;
    if (hidden_layers.length >= MAX_NUM_LAYERS) { return; }
    hidden_layers.push(DEFAULT_NUM_NEURONS);
    this.dis_between_layers = (this.neurons_area_width - neuron_d) / (hidden_layers.length - 1);

    this.props.on_network_change(hidden_layers);
  }

  private on_del_layer = () => {
    const hidden_layers = this.props.network_data;
    if (hidden_layers.length <= 1) { return; }
    hidden_layers.pop();
    this.dis_between_layers = (this.neurons_area_width - neuron_d) / (hidden_layers.length - 1);

    this.props.on_network_change(hidden_layers);
  }

  /** ****************** Training Paras Controller ******************/
  private get_layer_controller = () => {
    // Cannot change network structure while training
    const training_style:React.CSSProperties | undefined =
      this.props.is_training && { opacity: 0.5, pointerEvents: 'none' } ||
      undefined;
    const controller_style = this.props.network_data.length === 1 ? { justifyContent: 'center' } : undefined;

    const generate_btn_style = (is_clickable:boolean) => ((!this.props.is_custom_mode || is_clickable) ? 'disabled' : '');

    return <div styleName="diagram-controller-container">
      <div styleName="diagram-title">
        {this.props.intl.formatMessage({ id: 'network_title' })}
      </div>
      <div styleName="controller-wrapper" style={training_style}>
        <div styleName="layer-controller">
          <div styleName="control-btn-group">
            <div styleName={`btn-add ${generate_btn_style(this.props.network_data.length >= MAX_NUM_LAYERS)}`} onClick={this.on_add_layer} />
            <div styleName={`btn-del ${generate_btn_style(this.props.network_data.length <= 1)}`} onClick={this.on_del_layer} />
          </div>
          <div styleName="layer-info">
            {this.props.intl.formatMessage(
              { id: 'num_of_hidden_layers' },
              { number: this.props.network_data.length },
            )}
          </div>
        </div>
        <div styleName="neurons-controller" style={controller_style}>
          {this.props.network_data.map((val, index) => <div key={index} styleName="control-btn-group">
            <div styleName={`btn-add ${generate_btn_style(val >= MAX_NUM_NEURONS)}`}
                onClick={this.on_add_neuron.bind(this, index)} />
            <div styleName={`btn-del ${generate_btn_style(val <= 1)}`}
                onClick={this.on_del_neuron.bind(this, index)} />
          </div>)}
        </div>
      </div>
    </div>;
  }

  /** ****************** SVG ******************/

  /**
   * Handle hidden layers setting change after training
   * i.e. the net cannot be change immediately (the previous trained net should be kept)
   * set 0.6 as default opacity of undefined edges
   */
  private get_opacity = (layer_i:number, prev_node_i:number, next_node_i:number) => (this.state.weight[layer_i] && this.state.weight[layer_i][prev_node_i] && 0.2 + 0.8 * this.state.weight[layer_i][prev_node_i][next_node_i]) ||
      0.6
  /// IN & OUT PATHS
  private get_in_path = (index:number, distance:number = in_out_dis) => {
    const opacity = this.get_opacity(0, 0, index);
    const dx = arc_ratio * distance;
    const end_x = distance;
    const end_y = index * neuron_top_dis + neuron_d / 2;
    return <path key={'in' + index} stroke={input_color} fill="transparent" strokeDasharray="4,2"
        opacity={opacity}
        styleName={this.props.is_training ? 'animate-path' : ''}
        d={`M0 84 C ${dx} 84, ${end_x - dx} ${end_y}, ${end_x} ${end_y}`} />;
  }

  private get_out_path = (index:number, distance:number = in_out_dis) => {
    const opacity = this.get_opacity(this.state.weight.length - 1, index, 0);
    const dx = arc_ratio * distance;
    const start_x = svg_width - distance;
    const start_y = index * neuron_top_dis + neuron_d / 2;
    const end_x = svg_width;
    const end_y = 52;
    return <path key={'out' + index} stroke={transition_color} fill="transparent" strokeDasharray="4,2"
        opacity={opacity}
        styleName={this.props.is_training ? 'animate-path' : ''}
        d={`M${start_x} ${start_y} C ${start_x + dx} ${start_y}, ${end_x - dx} ${end_y}, ${end_x} ${end_y}`} />;
  }

  private get_in_out_paths = () => {
    const paths = [];

    if (this.props.network_data.length === 1) {
      for (let i = 0; i < this.props.network_data[0]; i++) {
        paths.push(this.get_in_path(i, SINGLE_DIS_LEFT));
        paths.push(this.get_out_path(i, SINGLE_DIS_LEFT));
      }
      return paths;
    }

    for (let i = 0; i < this.props.network_data[0]; i++) {
      paths.push(this.get_in_path(i));
    }
    for (let i = 0; i < this.props.network_data[this.props.network_data.length - 1]; i++) {
      paths.push(this.get_out_path(i));
    }
    return paths;
  }

  /// //// NEURONS AND PATHS BETWEEN LAYERS

  private get_neurons_and_paths = () => {
    const dx = arc_ratio_between_layers * this.dis_between_layers;
    const elements:JSX.Element[] = [];
    this.props.network_data.forEach((num_neuron, layer_index) => {

      if (this.props.network_data.length === 1) {
        for (let i = 0; i < num_neuron; i++) {
          const y = i * neuron_top_dis;
          elements.push(<use key={`n${layer_index}-${i}`} x={SINGLE_DIS_LEFT} y={y} xlinkHref="#neuron" />);
        }
        return;
      }

      const x = in_out_dis + layer_index * this.dis_between_layers;
      const last_end_x = in_out_dis + (layer_index - 1) * this.dis_between_layers + neuron_d;
      for (let i = 0; i < num_neuron; i++) {
        const y = i * neuron_top_dis;
        elements.push(<use key={`n${layer_index}-${i}`} x={x} y={y} xlinkHref="#neuron" />);
        if (layer_index === 0) { continue; }
        const path_y = y + neuron_d / 2;
        for (let last_i = 0; last_i < this.props.network_data[layer_index - 1]; last_i++) {
          const last_path_y = last_i * neuron_top_dis + neuron_d / 2;
          const opacity = this.get_opacity(layer_index, last_i, i);

          elements.push(
            <path key={`p${layer_index}-${last_i}-${i}`} stroke={transition_color} fill="transparent" strokeDasharray="4,2"
                opacity={opacity}
                styleName={this.props.is_training ? 'animate-path' : ''}
                d={`M${last_end_x} ${last_path_y} C ${last_end_x + dx} ${last_path_y}, ${x - dx} ${path_y}, ${x} ${path_y}`} />);
        }
      }
    });
    return elements;
  }

  private get_diagram = () => (
    <svg width="415" height="300" version="1.1" xmlns="http://www.w3.org/2000/svg" key="neuron">
      <defs>
        <circle id="path-1" cx="13.4210526" cy="13.4210526" r="13.4210526"></circle>
        <linearGradient x1="50%" y1="0%" x2="54.3927209%" y2="39.8901054%" id="linearGradient-3">
          <stop stopColor="#FFC97E" stopOpacity="0" offset="0%"></stop>
          <stop stopColor="#FF9948" offset="100%"></stop>
        </linearGradient>
        <filter x="-12.5%" y="-11.8%" width="125.0%" height="123.5%" filterUnits="objectBoundingBox" id="filter-4">
          <feGaussianBlur stdDeviation="1.57894737" in="SourceGraphic"></feGaussianBlur>
        </filter>
        <linearGradient x1="92.985509%" y1="94.4351722%" x2="28.4162663%" y2="5.56482775%" id="linearGradient-5">
          <stop stopColor="#0090FF" stopOpacity="0" offset="0%"></stop>
          <stop stopColor="#007EFF" offset="56.2768241%"></stop>
          <stop stopColor="#0057FF" offset="100%"></stop>
        </linearGradient>
        <filter x="-38.7%" y="-41.1%" width="177.4%" height="182.1%" filterUnits="objectBoundingBox" id="filter-6">
          <feGaussianBlur stdDeviation="3.15789474" in="SourceGraphic"></feGaussianBlur>
        </filter>
        <g stroke="none" id="neuron" strokeWidth="1" fill="none" fillRule="evenodd">
          <g transform="translate(4.000000, 4.000000)">
            <g transform="translate(1.578947, 1.578947)">
              <mask id="mask-2" fill="white">
                <use xlinkHref="#path-1" />
              </mask>
              <g id="Mask">
                <use fill="#3DD1AE" xlinkHref="#path-1" />
                <use fill="#FFFFFF" xlinkHref="#path-1" />
              </g>
              <path d="M16.1842105,48.1578947 C25.9945317,48.1578947 33.9473684,40.2050581 33.9473684,30.3947368 C33.9473684,23.8545227 32.1052632,16.3545227 28.4210526,7.89473684 C22.7309144,11.8518951 18.651967,14.483474 16.1842105,15.7894737 C4.7900327,21.8195631 -3.94736842,8.4845603 -3.94736842,13.4210526 C-3.94736842,23.2313738 6.37388931,48.1578947 16.1842105,48.1578947 Z" fill="url(#linearGradient-3)" filter="url(#filter-4)" mask="url(#mask-2)"></path>
              <path d="M7.5,19.7368421 C10.8348322,19.7368421 11.3288396,15.6965422 13.4210526,13.4210526 C15.2952773,11.3826472 20.5263158,4.5663222 20.5263158,1.57894737 C20.5263158,-4.74325964 13.822207,-3.15789474 7.5,-3.15789474 C1.177793,-3.15789474 -3.94736842,1.96726668 -3.94736842,8.28947368 C-3.94736842,14.6116807 1.177793,19.7368421 7.5,19.7368421 Z" fill="url(#linearGradient-5)" filter="url(#filter-6)" mask="url(#mask-2)"></path>
            </g>
            <path d="M15,30 C23.2842712,30 30,23.2842712 30,15 C30,6.71572875 23.2842712,0 15,0 C6.71572875,0 0,6.71572875 0,15 C0,23.2842712 6.71572875,30 15,30 Z" stroke="#FF9948" strokeWidth="0.789473684" strokeLinecap="round" strokeLinejoin="round" strokeDasharray="0.7894736842105263,1.578947368421053"></path>
          </g>
        </g>

      </defs>
      {this.get_in_out_paths()}
      {this.get_neurons_and_paths()}
    </svg>)

  /**
   * Compute weight of every path based on the net resulted from convnetjs trainer.
   * **Do not make any change without fully understanding of this function**
   * @param net the stringified net from convnetjs
   */
  private update_weight(net:string) {
    const fc_layers:FullyConnLayer[] = JSON.parse(net).layers.filter((layer:ILayer) => layer.layer_type === 'fc');
    const filters_range = [Infinity, -Infinity];
    const filters = fc_layers.map((fc_layer, index) => {
      let _filter:number[][] = [];
      if (index === 0) {// input -> first hidden layer, reduce weight
        // original weight structure: num_of_classifications -> num_node_of_first_hidden_layer
        // should be converted to
        // 1 (only one input illustration) -> num_node_of_first_hidden_layer
        _filter = [fc_layer.filters.map((filter) => {
          let t = 0;
          let n = 0;
          for (const key in filter.w) {
            t += filter.w[key];
            n++;
          }
          const num = t / n;
          filters_range[0] = Math.min(filters_range[0], num);
          filters_range[1] = Math.max(filters_range[1], num);
          return num;
        })];
      } else if (index === fc_layers.length - 1) { // last hidden layer -> output, reduce weight
        // original weight structure: num_node_of_last_hidden_layer -> num_of_classifications
        // should be converted to
        // num_node_of_last_hidden_layer -> 1 (only one output illustration)
        for (let i = 0; i < fc_layer.num_inputs; i++) {
          let t = 0;
          fc_layer.filters.forEach((filter) => {
            t += filter.w[i];
          });
          const num = t / fc_layer.filters.length;
          filters_range[0] = Math.min(filters_range[0], num);
          filters_range[1] = Math.max(filters_range[1], num);
          _filter[i] = [num];
        }
      } else {
        for (let last_node_i = 0; last_node_i < fc_layer.num_inputs; last_node_i++) {
          _filter[last_node_i] = [];
          fc_layer.filters.forEach((filter, next_node_i) => {
            const num = filter.w[last_node_i];
            filters_range[0] = Math.min(filters_range[0], num);
            filters_range[1] = Math.max(filters_range[1], num);
            _filter[last_node_i][next_node_i] = num;
          });
        }
      }
      return _filter;
    });

    // justify range to [0, 1]
    const justify_range = (num:number) => (num - filters_range[0]) / (filters_range[1] - filters_range[0]);
    const weight = filters.map((layer) => layer.map((last_node) => last_node.map((next_node) => justify_range(next_node))));
    this.setState({ weight });
  }

  UNSAFE_componentWillReceiveProps(next_props:DiagramProps) {
    if (next_props.is_training && next_props.net && next_props.net !== this.props.net) {
      this.update_weight(next_props.net);
    }

    if (next_props.network_data.length !== this.props.network_data.length) {
      this.dis_between_layers = (this.neurons_area_width - neuron_d) / (next_props.network_data.length - 1);
    }
  }

  render() {
    return <div styleName="diagram-container">
      {this.get_layer_controller()}
      {this.get_diagram()}
    </div>;
  }
}

function map_state_to_props(state:StoreState) {
  return {
    network_data: state.model_state.hidden_layers,
    is_training: state.training_state.training_status === 'training',
    net: state.model_state.net,
    is_custom_mode: state.training_state.is_custom_mode,
  };
}

function map_dispatch_to_props(dispatch:Dispatch) {
  return bindActionCreators(
    {
      on_network_change: change_hidden_layer,
    },
    dispatch);
}

export const NNDiagramComponent =
  connect(
    map_state_to_props,
    map_dispatch_to_props,
  )(injectIntl((NeuralNetworkDiagram)));
